import env from "../config/env.js";
import { RESTDataSource, AugmentedRequest } from "@apollo/datasource-rest";
import { ItemModel, ItemStockModel, StockInput, StockModel } from "../graphql/models.js";
import { KeyValueCache } from "@apollo/utils.keyvaluecache";
import _ from "lodash";

export default class StockAPI extends RESTDataSource {
  override baseURL = `${env.API_URL}/api/`;
  private token: string;

  constructor(options: { token: string; cache: KeyValueCache }) {
    super(options);
    this.token = options.token;
  }

  override willSendRequest(_path: string, request: AugmentedRequest) {
    request.headers["usertoken"] = this.token;
  }

  async getStockById(stockId: string) {
    return await this.get<StockModel>(`stock/${stockId}`);
  }

  async getStockByItemId(itemid: string) {
    return await this.get<StockModel[]>(`stock/item/${itemid}`);
  }

  async getStockByItemType(itemtype: string) {
    return await this.get<ItemStockModel[]>(`stock/type/${itemtype}`);
  }

  async createStock(stockitem: StockInput) {
    // console.log("params currently: ", item);

    try {
      // return await this.post<ItemModel>(`${type}/new`, {
      return await this.post<StockModel>("stock/new", {
        body: stockitem,
      });
    } catch (error: any) {
      console.log("graphql error: ", error?.extensions);
      return null;
    }
  }

  async updateStock(stockitem: StockInput) {
    // console.log("token currently: ", this.token);

    try {
      // return await this.put<ItemModel>(`${type}/${item.id}`, {
      return await this.put<StockModel>(`stock/update`, {
        body: stockitem,
      });
    } catch (error: any) {
      console.log("graphql error: ", error?.extensions);
      return null;
    }
  }

  async deleteStock(id: string) {
    // console.log("token currently: ", this.token);

    try {
      return await this.delete<string>(`stock/delete/${id}`);
    } catch (error: any) {
      console.log("graphql error: ", error?.extensions);
      return null;
    }
  }
}
