import { Request, Response, NextFunction } from "express";
import { verifyUserToken } from "../lib/session.js";
import { userPayloadSchema } from "../lib/types.js";
import { User } from "../auth/models/user.js";
import { BadRequestError } from "@manprtickets/common";
import mongoose from "mongoose";
import { log } from "console";

export const currentUser = async (
  req: Request,
  res: Response,
  next: NextFunction
) => {
  // console.log("session: ", req.session.jwt);



  if (!req.session?.jwt) {
    return next();
  }

  try {
    // console.log("entering into verify");

    const payload = await verifyUserToken(req.session.jwt);

    // log("Payload: ", payload);
    const { id, email, firstName, lastName } = userPayloadSchema.parse({
      ...payload,
      // first_name: "Toji",
      // last_name: "Varghese",
    });
    // console.log("id: ", id, " email: ", email);

    const existingUser = await User.findOne({ email }); // _id: new mongoose.Types.ObjectId(id)
    // console.log("Existing user: ", existingUser);

    if (!existingUser) {
      throw new BadRequestError("Sorry you are not a valid user in the System");
    }
    req.currentUser = {
      id: existingUser.id,
      email: existingUser.email,
      // firstName: existingUser.firstName,
      // lastName: existingUser.lastName,
    };
  } catch (error) {}

  next();
};
