import env from "../config/env.js";
import { RESTDataSource, AugmentedRequest } from "@apollo/datasource-rest";
import {
  AuthModel,
  SignInInput,
  SignUpInput,
  UserModel,
} from "../graphql/models.js";
import { KeyValueCache } from "@apollo/utils.keyvaluecache";
import _ from "lodash";

export default class AuthAPI extends RESTDataSource {
  override baseURL = `${env.API_URL}/api/`;
  private token: string;

  constructor(options: { token: string; cache: KeyValueCache }) {
    super(options);
    this.token = options.token;
  }

  override willSendRequest(_path: string, request: AugmentedRequest) {
    request.headers["usertoken"] = this.token;
  }

  async getCurrentUser() {
    return await this.get<UserModel>("users/currentuser");
  }

  async signin(credentials: SignInInput) {
    // console.log("params currently: ", billitem);

    try {
      // return await this.post<ItemModel>(`${type}/new`, {
      return await this.post<AuthModel>("users/signin", {
        body: credentials,
      });
    } catch (error: any) {
      console.log("graphql error: ", error?.extensions);
      return null;
    }
  }

  async signup(credentials: SignUpInput) {
    // console.log("params currently: ", credentials);

    try {
      // return await this.post<ItemModel>(`${type}/new`, {
      return await this.post<AuthModel>("users/signup", {
        body: credentials,
      });
    } catch (error: any) {
      console.log("graphql error: ", error?.extensions);
      return null;
    }
  }

  async signout() {
    // console.log("token currently: ", this.token);

    try {
      // return await this.put<ItemModel>(`${type}/${item.id}`, {
      return await this.post<AuthModel>(`users/signout`);
    } catch (error: any) {
      // console.log("graphql error: ", error?.extensions);
      return null;
    }
  }
}
