import { Request, Response, NextFunction } from "express";
import * as _ from 'lodash';
import { Stock } from "../models/stock";

const postNewStockEntry = async (
  req: Request,
  res: Response,
  next: NextFunction
) => {
  const { itemid, itemtype, quantity, qtytype, unit, price, gst, discount } = req.body;

  const modelName = _.upperFirst(itemtype);

  // console.log("new stock: ", req.body);
  try {
    const stockitem = Stock.build({
      itemid,
      itemtype: modelName,
      quantity,
      qtytype,
      unit,
      price,
      gst,
      discount,
      userId: req.currentUser!.id,
      // itemModel: modelName,
    });

    await stockitem.save();
    // console.log("product saved", stockitem);
    res.status(201).send(stockitem);
  } catch (error) {
    console.log("error: ", error);
    
  }
};

export default postNewStockEntry;
