const postSignin = async (req: Request, res: Response, next: NextFunction) => {
  const { email, password } = req.body;

  // console.log("email in auth logs in: ", email, "password: ", password);

  const existingUser = await User.findOne({ email });
  if (!existingUser) {
    throw new BadRequestError("Sorry you entered wrong login Credentials");
  }
  const passwordsMatch = await Password.compare(
    existingUser.password,
    password
  );

  if (!passwordsMatch) {
    throw new BadRequestError("Invalid Credentials");
  }

  // console.log("existing user: ", existingUser);
  

  // sign, create token and send the token to session

  // const userJwt = await encryptData({
  //   id: "675d263dab93ec2b8dc1cb53",
  //   email: "tester11@test.com",
  // });

  const userJwt = await generateUserToken({
    id: existingUser.id,
    email: existingUser.email,
    firstName: existingUser.firstName,
    lastName: existingUser.lastName,
    // first_name: "Toji",
    // last_name: "Varghese",
  });

  // console.log("returned token: ", userJwt);

  req.session.jwt = userJwt;

  // req.session.jwt = userJwt;
  // req.session.isAuthenticated = true;
  // req.session.userid = existingUser.id;
  // req.session.useremail = existingUser.email;

  // res.cookie("session", userJwt, {
  //   httpOnly: false,
  //   // secure: true,
  //   sameSite: "none", // Enforce secure cookies & // Prevent CSRF attacks by setting sameSite
  //   maxAge: 1 * 24 * 60 * 60 * 1000, // 1 days
  //   path: '/'
  // });

  res.status(200).send({
    user: {
      id: existingUser.id,
      firstName: existingUser.firstName,
      lastName: existingUser.lastName,
      email: existingUser.email,
      phone: existingUser.phone,
    },
    token: userJwt,
  });
};

export default postSignin;
