import { Resolvers } from "../../types/types";

export const stockResolvers: Resolvers = {
  Query: {
    stockById: (_, { stockid }, { dataSources }) => {
      return dataSources.stockAPI.getStockById(stockid);
    },
    stockByItemId: (_, { itemid }, { dataSources }) => {
      return dataSources.stockAPI.getStockByItemId(itemid);
    },
    stockByItemType: (_, { itemType }, { dataSources }) => {
      return dataSources.stockAPI.getStockByItemType(itemType);
    },
  },
  ItemStock: {
    item: ({ itemid }, _, { dataSources }) => {
      return dataSources.itemAPI.getPart(itemid);
    },
  },
  Mutation: {
    createStock: async (_, { input }, { dataSources }) => {
      try {
        // console.log("New item created: ", input);
        const newStockItem = await dataSources.stockAPI.createStock(input);

        return {
          code: 200,
          success: true,
          message: "Item created successfully",
          item: newStockItem,
        };
      } catch (err: any) {
        return {
          code: err.extensions.response.status,
          success: false,
          message: err.extensions.response.body,
          item: null,
        };
      }
    },
    updateStock: async (_, { input }, { dataSources }) => {
      try {
        const editStockItem = await dataSources.stockAPI.updateStock(input);
        // console.log("New item created: ", newItem);

        return {
          code: 200,
          success: true,
          message: "Item updated successfully",
          item: editStockItem,
        };
      } catch (err: any) {
        return {
          code: err.extensions.response.status,
          success: false,
          message: err.extensions.response.body,
          item: null,
        };
      }
    },
    deleteStock: async (_, { stockId }, { dataSources }) => {
      try {
        const message = await dataSources.stockAPI.deleteStock(stockId);
        // console.log("return value: ", message);

        return {
          code: 200,
          success: true,
          message: "Item deleted successfully",
          item: message as string,
        };
      } catch (err: any) {
        return {
          code: err.extensions.response.status,
          success: false,
          message: err.extensions.response.body,
          item: "",
        };
      }
    },
  },
};
