// import jose, { JWE, JWK, JWS } from "node-jose";
// import fs from "fs";
// import { error } from "console";
// import { result } from "lodash";
import { jwtDecode } from "jwt-decode";
import jose from "node-jose";

const { JWE, JWK, JWS } = jose;

const joseutils = () => {
  // const privateKey = fs.readFileSync("private_key.pem");
  // const publicKey = fs.readFileSync("public_key.pem");

  const privateKey = process.env.JWT_PRIVATEKEY;
  const publicKey = process.env.JWT_PUBLICKEY;

  async function getKeys() {
    // const makeKey = async (pem: Buffer<ArrayBufferLike>) => await JWK.asKey(pem, "pem");
    const makeKey = async (pem: string) => await JWK.asKey(pem, "pem");

    const pub_key = await makeKey(publicKey as string);
    const prv_key = await makeKey(privateKey as string);

    // console.log("public: ", pub_key, "private: ", prv_key);
    

    return { pub_key, prv_key };
  }

  async function sign(raw: unknown) {
    const { prv_key } = await getKeys();

    if (!raw) {
      throw new Error("Missing Data");
    }

    const inputBuffer = JSON.stringify(raw);

    const jwsToken = await JWS.createSign({ format: "compact" }, prv_key)
      .update(inputBuffer)
      .final()
      .then(function (result) {
        return result;
      });

    return jwsToken;
  }

  async function verify(jwsToken: string) {
    const { pub_key } = await getKeys();

    if (!jwsToken) {
      throw new Error("Missing JWS Token");
    }

    // const inputBuffer = JSON.stringify(raw);

    const verifyResult = await JWS.createVerify(pub_key)
      .verify(jwsToken)
      .then((result) => {
        const buffertoString = jose.util.base64url.encode(result.payload);
        // console.log("string converted", buffertoString);
        
        return jwtDecode(buffertoString, { header: true });

      })
      .catch((error) => {
        console.log("returned ", error);
      });

    return verifyResult;
  }

  async function encrypt(raw: unknown) {
    const { pub_key } = await getKeys();

    if (!raw) {
      throw new Error("Missing Data");
    }
    const inputBuffer = JSON.stringify(raw);
    return await JWE.createEncrypt(pub_key).update(inputBuffer).final();
  }

  async function decrypt(encrypted: string) {
    const { prv_key } = await getKeys();
    if (!encrypted) {
      throw new Error("Missing encrypted arg");
    }
    // const buffer = Buffer.from(JSON.stringify(encrypted));
    return await JWE.createDecrypt(prv_key).decrypt(encrypted);
  }

  return { encrypt, decrypt, sign, verify };
};

export default joseutils;
