import { Request, Response, NextFunction } from "express";
import { Order, OrderDoc } from "../models/order.js";
import { Address } from "../models/address.js";

import _ from "lodash";

export const getUserOrders = async (
  req: Request,
  res: Response,
  next: NextFunction
) => {
  try {
    const orders = await Order.find({
      userId: req.currentUser?.id,
      status: "Complete",
    });
    // console.log("orders: ", orders);

    res.status(201).send(orders);
  } catch (error) {
    console.log(error);
  }
};

export const getOrdersByUser = async (
  req: Request,
  res: Response,
  next: NextFunction
) => {
  try {
    // console.log("orders is called");
    const orders = await Order.find({
      userId: req.currentUser?.id,
      status: "Complete",
    })
      .populate({
        path: "items.itemid",
        select: "title description image",
        transform: (doc, id) =>
          doc == null
            ? null
            : {
                prodid: id,
                title: doc.title,
                image: doc.image,
                description: doc.description,
              },
      })
      .exec();

    const orderList = orders.map((order) => ({
      id: order.id,
      orderDate: order.orderDate,
      userId: order.userId,
      status: order.status,
      expiresAt: order.expiresAt,
      totalQuantity: order.totalQuantity,
      orderValue: order.orderValue,
      shipping: order.shipId,
      items: transformOrderItems(order),
    }));

    // console.log("orders: ", orderList);
    res.status(201).send(orderList);
  } catch (error) {
    console.log(error);
  }
};

function transformOrderItems(order: OrderDoc) {
  const newItems = order.items.map((item) => ({
    itemid: _.get(item, "itemid.prodid]"),
    orderId: _.get(item, "orderId"),
    itemtype: _.get(item, "itemtype"),
    itemModel: _.get(item, "itemModel"),
    title: _.get(item, "itemid.title"),
    description: _.get(item, "itemid.description"),
    image: _.get(item, "itemid.image"),
    quantity: _.get(item, "quantity"),
    price: _.get(item, "price"),
    totalPrice: _.get(item, "totalPrice"),
  }));
  return newItems;
}

export const getOrderById = async (
  req: Request,
  res: Response,
  next: NextFunction
) => {
  try {
    const order = await Order.findById({
      _id: req.params.orderid,
    });
    // console.log("order: ", order);

    res.status(201).send(order);
  } catch (error) {
    console.log(error);
  }
};

export const getAddresses = async (
  req: Request,
  res: Response,
  next: NextFunction
) => {
  // console.log("address is called: ", req.currentUser?.id);

  const addresses = await Address.find({ userId: req.currentUser?.id });
  // console.log("addresses: ", addresses);

  res.status(201).send(addresses);
};

export const getAddressById = async (
  req: Request,
  res: Response,
  next: NextFunction
) => {
  // console.log("address is called: ", req.params.id);

  const address = await Address.findById(req.params.id);
  // console.log("address: ", address);

  res.send(address);
};
