import mongoose from "mongoose";
import { OrderStatus } from "../../types/order-status.js";
// import { ItemTypes } from "../../types/item-types";
const Schema = mongoose.Schema;

// const itemtypes = ["Product", "Equipment", "Spare", "Package"];

interface OrderItemAttrs {
  itemid: string;
  orderId: string;
  // itemtype: string;
  // itemModel: string;
  quantity: number;
  price: number;
  totalPrice: number;
}

export interface OrderItemDoc extends mongoose.Document {
  itemid: string;
  orderId: string;
  // itemtype: string;
  // itemModel: string;
  quantity: number;
  price: number;
  totalPrice: number;
  // title: string;
  // image: string;
  // description: string;
}

interface OrderItemModel extends mongoose.Model<OrderItemDoc> {
  build(attrs: OrderItemAttrs): OrderItemDoc;
}

export const orderItemSchema = new mongoose.Schema(
  {
    // itemid: {
    //   type: Schema.Types.ObjectId,
    //   required: true,
    //   refPath: "items.itemModel",
    // },
    itemid: {
      type: Schema.Types.ObjectId,
      required: true,
      ref: "Item",
    },
    orderId: {
      type: Schema.Types.ObjectId,
      ref: "Order",
      required: true,
    },
    // itemtype: {
    //   type: String,
    //   enum: Object.values(ItemTypes),
    //   default: ItemTypes.Product,
    //   required: true,
    // },
    quantity: {
      type: Number,
      default: 0,
      required: true,
    },
    price: {
      type: Number,
      default: 0.0,
      required: true,
    },
    totalPrice: {
      type: Number,
      default: 0.0,
      required: true,
    },
    // title: {
    //   type: String,
    // },
    // description: {
    //   type: String,
    // },
    // image: {
    //   type: String,
    // },
    // itemModel: {
    //   type: String,
    //   required: true,
    //   //   enum: itemtypes,
    //   enum: Object.values(ItemTypes),
    //   default: ItemTypes.Product,
    // },
  },
  {
    toJSON: {
      transform(doc, ret: any) {
        ret.id = ret._id;
        delete ret._id;
      },
    },
  }
);

// cartItemSchema.statics.build = (attrs: CartItemAttrs) => {
//   return new CartItem(attrs);
// };

// const CartItem = mongoose.model<CartItemDoc, CartItemModel>(
//   "CartItem",
//   cartItemSchema
// );

interface OrderAttrs {
  userId: string;
  status: OrderStatus;
  items: [typeof orderItemSchema] | [];
  orderDate: Date;
  expiresAt: Date;
  totalQuantity: number;
  orderValue: Number;
  //   paymentMode: string;
  shipId?: string;
  //   paymentId: string;
}

export interface OrderDoc extends mongoose.Document {
  id: string;
  userId: string;
  status: OrderStatus;
  items: [typeof orderItemSchema] | [];
  orderDate: Date;
  expiresAt: Date;
  totalQuantity: number;
  orderValue: Number;
  //   paymentMode: string;
  shipId?: string;
  //   paymentId: string;
}

interface OrderModel extends mongoose.Model<OrderDoc> {
  build(attrs: OrderAttrs): OrderDoc;
}

const orderSchema = new mongoose.Schema(
  {
    userId: {
      type: Schema.Types.ObjectId,
      ref: "User",
      required: true,
    },
    status: {
      type: String,
      required: true,
      enum: Object.values(OrderStatus),
      default: OrderStatus.Created,
    },
    items: {
      type: [orderItemSchema],
    },
    orderDate: {
      type: mongoose.Schema.Types.Date,
      required: true,
    },
    expiresAt: {
      type: mongoose.Schema.Types.Date,
    },
    totalQuantity: {
      type: Number,
      default: 0,
      required: true,
    },
    orderValue: {
      type: Number,
      default: 0.0,
      required: true,
    },
    // paymentMode: {
    //   type: String,
    //   required: true,
    // },
    shipId: {
      type: Schema.Types.ObjectId,
      ref: "Address",
      // required: true,
    },
    // paymentId: {
    //   type: Schema.Types.ObjectId,
    //   ref: "Payment",
    //   required: true,
    // },
  },
  {
    toJSON: {
      transform(doc, ret: any) {
        ret.id = ret._id;
        delete ret._id;
      },
    },
  }
);

orderSchema.statics.build = (attrs: OrderAttrs) => {
  return new Order(attrs);
};

const Order = mongoose.model<OrderDoc, OrderModel>("Order", orderSchema);

export { Order };
