export function getOrder(idStr: string) {
  const data = JSON.stringify({
    query: `query OrderByID($orderid: ID!) {
      orderByID(orderid: $orderid) {
        id
        userId
        status
        orderDate
        totalQuantity
        orderValue
        items {
          orderitem {
            id
            itemType
            title
            description
            image
            unit
            dimensions
          }
          quantity
          price
          totalPrice
        }
        shipping {
          id
          email
          receiver
          phone
          house
          street
          pincode
          city
        }
      }
    }`,
    variables: {
      orderid: idStr,
    },
  });

  return data;
}