export enum OrderStatus {
  // When the order has been created, but the
  Created = "Created",

  // The product the order is trying to reserve has already been
  // reserved, or when the user has cancelled the order
  // The order expires before payment
  Cancelled = "Cancelled",

  // The order has successfully reserved the product
  AwaitingPayment = "Awaiting:payment",

  // The order has reserved the product and the user has provided payment successfully
  Complete = "Complete",
}
