import { Request, Response, NextFunction } from "express";
import * as _ from "lodash";
import { BillItem } from "../models/bill-item.js";
import { Stock } from "../../stock/models/stock.js";
import mongoose from "mongoose";

const postNewBill = async (req: Request, res: Response, next: NextFunction) => {
  const {
    itemid,
    itemtype,
    quantity,
    unit,
    qtytype,
    price,
    gst,
    discount,
    billtotal,
    shipping,
  } = req.body;

  // const modelName = _.upperFirst(itemtype);

  // console.log("new product: ", req.body);

  const session = await mongoose.startSession();
  session.startTransaction();
  try {
    const billitem = BillItem.build({
      itemid,
      itemtype,
      quantity,
      qtytype,
      unit,
      price,
      gst,
      discount,
      billtotal,
      shipping,
      userId: req.currentUser!.id,
    });



    const stockitem = Stock.build({
      itemid,
      itemtype,
      quantity,
      qtytype: "Stock",
      unit,
      price,
      gst,
      discount,
      userId: req.currentUser!.id,
    });
    // console.log("product saved", stockitem);
    await billitem.save();
    await stockitem.save();

    await session.commitTransaction();

    res.status(201).send(billitem);
  } catch (error) {
    console.log("error: ", error);
    await session.abortTransaction();
    // return null;
  }
};

export default postNewBill;
