import { Document, Page, Text, View } from "@react-pdf/renderer";
import styles from "../report_styles.js";
import Footer from "../snippets/Footer.js";
import BillTitle from "../snippets/bill_title.js";
import BoxNoDate from "../snippets/box_nodate.js";
import { formattedIDNumber, formattedDate } from "../../lib/util_fns.js";
import BoxAddress from "../snippets/box_address.js";
import { OrderType } from "../../types/zod-schemas.js";
import BOMTableHead from "../snippets/bom-table-head.js";
import BOMTableBody from "../snippets/bom-table-body.js";
import BOMFooter from "../snippets/bom-footer.js";
import Signature from "../snippets/signature.js";

const OrderDetail = ({ order }: { order: OrderType }) => {
  // console.log("type of date before : ", typeof order.orderDate);

  return (
    <Document>
      <Page size="A4" style={styles.page}>
        <BillTitle name="Original Tax Invoice/ Bill of Supply/ Cash Memo" />
        <View style={styles.orderNumRow}>
          <BoxNoDate
            name="Order"
            num_fld={formattedIDNumber(order.id, 10)}
            date_fld={formattedDate(order.orderDate)}
            style={{ alignItems: "flex-start" }}
          />
          <BoxNoDate
            name="Invoice"
            num_fld={formattedIDNumber(order.id, 10)}
            date_fld={formattedDate(order.orderDate)}
            style={{ alignItems: "flex-end" }}
          />
        </View>
        <View style={styles.addressRow}>
          <BoxAddress name="Sold By" address={order.shipping} />
          <BoxAddress name="Shipping" address={order.shipping} />
          <BoxAddress name="Billing" address={order.shipping} />
        </View>
        <View style={styles.tableRow}>
          <BOMTableHead />
          <BOMTableBody items={order.items} />
          <BOMFooter total={order.orderValue} />
        </View>
        <Signature />
        <Footer />
      </Page>
    </Document>
  );
};

export default OrderDetail;
