import { Request, Response, NextFunction } from "express";
import mongoose from "mongoose";
import { Item } from "../../item/models/item";
import { Stock } from "../models/stock";

export const getStockByType = async (
  req: Request,
  res: Response,
  next: NextFunction
) => {
  const stocks = await Stock.find({ itemtype: "Product" });
};

export const getItemStock = async (
  req: Request,
  res: Response,
  next: NextFunction
) => {
  const allItemStock = await Item.aggregate([
    {
      $lookup: {
        from: "stocks",
        localField: "_id",
        foreignField: "itemid",
        as: "stock",
      },
    },
    {
      $unwind: {
        path: "$stock",
        preserveNullAndEmptyArrays: true,
      },
    },
    {
      $project: {
        type: "Product",
        _id: 0,
        unit: 1,
        title: 1,
        image: 1,
        id: { $toString: "$_id" },
        "stock.quantity": 1,
        "stock.stockid": "$stock._id",
        "stock.price": 1,
        "stock.gst": 1,
        "stock.discount": 1,
      },
    },
  ]);

  res.send(allItemStock);
};

export const getStockByItemId = async (
  req: Request,
  res: Response,
  next: NextFunction
) => {
  console.log("from stock by item: ", req.params.itemid);

  let stockItemArr = await Stock.find({
    itemid: new mongoose.Types.ObjectId(req.params.itemid),
  });

  // console.log("selected: ", stockItemArr);

  res.send(stockItemArr);
};

export const getStockByItemType = async (
  req: Request,
  res: Response,
  next: NextFunction
) => {
  // console.log("from stock by item: ", req.params.itemid);

  // let stockItemArr = await Stock.find({
  //   itemtype: req.params.itemtype,
  // });

  let stockItemArr = await Stock.aggregate([
    {
      $match: { itemtype: req.params.itemtype },
    },
    {
      $addFields: {
        itemid: { $toString: "$itemid" },
        id: { $toString: "$_id" },
        userId: { $toString: "$userId" },
      },
    },
    {
      $project: {
        _id: 0,
        id: 1,
        itemid: 1,
        quantity: 1,
        qtytype: 1,
        price: 1,
        gst: 1,
        discount: 1,
        userId: 1,
      },
    },
  ]);
  // console.log("selected: ", stockItemArr);

  res.send(stockItemArr);
};

export const getStockById = async (
  req: Request,
  res: Response,
  next: NextFunction
) => {
  // console.log("get stock entry: ", req.params.id);

  const stockItem = await Stock.findOne({
    _id: new mongoose.Types.ObjectId(req.params.id),
  });

  // console.log(stockItem);

  res.send(stockItem);
};
