import { requireAuth } from "@manprtickets/common";
import express from "express";
import cancelOrder from "./delete.js";
import { getAddressById, getAddresses, getOrderById, getOrdersByUser, getUserOrders } from "./index.js";
import { createAddress, createOrder } from "./new.js";
import { completeOrder, updateOrder } from "./update.js";
import { downloadPDF, generateOrderPdf } from "./pdf-generate.js";

const router = express.Router();

router.get("/", requireAuth, getUserOrders);
router.get("/address", requireAuth, getAddresses);
router.get("/address/:id", requireAuth, getAddressById); // 
router.get("/:orderid", requireAuth, getOrderById); // 
router.get("/genpdf/:orderid", requireAuth, generateOrderPdf); // 
router.get("/downpdf/:orderid", requireAuth, downloadPDF); // 
router.post("/", requireAuth, createOrder);
router.post("/address", requireAuth, createAddress);
router.patch("/complete/:orderid", requireAuth, completeOrder);
router.patch("/:orderid", requireAuth, updateOrder);
router.delete("/:orderid", requireAuth, cancelOrder);
// router.put("/", requireAuth, updateOrder);

export { router as orderRouter };
