import { Request, Response, NextFunction } from "express";
import { Item } from "../models/item.js";
import mongoose from "mongoose";

export const getItemBySearch = async (
  req: Request,
  res: Response,
  next: NextFunction
) => {
  let products;

  const searchString = req.params.keystr;
  const itemType = req.params.itemtype;
  // console.log(" search: ", searchString, " type: ", itemType);

  let aggregate = Item.aggregate([
    { $match: { itemType: itemType } },
    { $addFields: { id: "$_id" } },
  ]);

  if (searchString !== "all") {
    aggregate = Item.aggregate([
      { $match: { itemType: itemType, $text: { $search: searchString } } },
      { $addFields: { id: "$_id" } },
    ]);
  }
  const results = await aggregate.exec();

  // console.log("results: ", results);

  //   res.status(200).send(products);
  res.status(200).send(results);
};

export const getItemById = async (
  req: Request,
  res: Response,
  next: NextFunction
) => {
  const itemId = req.params.idstr;
  // console.log("in getItemById:", itemId);

  const itemObject = await Item.findById(itemId);
  // console.log("result: ", itemObject);

  res.status(200).send(itemObject);
};

export const selectItemsByType = async (
  req: Request,
  res: Response,
  next: NextFunction
) => {
  const itemType = req.params.typeStr;

  const items = await Item.find(
    { itemType },
    { key: "$_id", label: "$title", _id: 0 }
  );

  // console.log("select: ", itemType, " list: ", items);

  res.send(items);
};
