import { Text, View } from "@react-pdf/renderer";
import styles from "../report_styles.js";

const BOMFooter = ({ total }: { total: number }) => {
  return (
    <View style={styles.tfootRow}>
      <View style={{ flexDirection: "column" }}>
        <View style={styles.tcellm}>
          <Text style={styles.theadText}>Tax Total</Text>
        </View>
        <View style={styles.tcellm}>
          <Text style={styles.text}>{total}</Text>
        </View>
      </View>
      <View style={{ flexDirection: "column" }}>
        <View style={styles.tcelll}>
          <Text style={styles.theadText}>Grand Total</Text>
        </View>
        <View style={styles.tcelll}>
          <Text style={styles.text}>{total}</Text>
        </View>
      </View>
    </View>
  );
};

export default BOMFooter;
