import { Request, Response, NextFunction, query } from "express";
import OrderDetail from "../../reports/order/order_detail.js";
import { renderToFile, renderToStream } from "@react-pdf/renderer";
import fetch from "node-fetch";
import env from "././../../config/env.js";
import { orderSchema } from "../../types/zod-schemas.js";
import { getOrder } from "../../graphql/queries/get-order.js";
import fs, { stat } from "fs";
import { NotFoundError } from "@manprtickets/common";
import { fileURLToPath } from "url";
import path from "path";

const { dirname } = path;

export const downloadPDF = async (
  req: Request,
  res: Response,
  next: NextFunction
) => {
  const queryOrderData = getOrder(req.params.orderid);
  
  const response = await fetch(`${env.API_URL}/graphql`, {
    method: "POST",
    body: queryOrderData,
    headers: {
      "Content-Type": "application/json",
      usertoken: req.session?.jwt as string,
    },
  });
  const order = await response.json();

  if (!order) {
    return res.status(404).send("Order not found");
  }

  const orderData = order?.data.orderByID;

  const validatedOrder = orderSchema.safeParse(orderData);

  if (!validatedOrder.success) {
    console.log(validatedOrder.error);
    return res.status(400).send("Invalid order data");
  }

  // console.log("order: ", validatedOrder.data);

  const pdfStream = await renderToStream(
    <OrderDetail order={validatedOrder.data} />
  );

  res.setHeader("Content-Type", "application/pdf");
  res.setHeader("Content-Disposition", 'attachment; filename="order-doc.pdf"');

  pdfStream.pipe(res);
  pdfStream.on("end", () => console.log("Done streaming, response sent"));
};

