import { Request, Response, NextFunction } from "express";
import * as _ from "lodash";
import { NotAuthorizedError, NotFoundError } from "@manprtickets/common";
import { Stock } from "../models/stock";

const deleteStockEntry = async (
  req: Request,
  res: Response,
  next: NextFunction
) => {
  const stockItem = await Stock.findById(req.params.id);

  if (!stockItem) {
    throw new NotFoundError();
  }

  // console.log("stock item: ", stockItem);

  if (stockItem.userId.valueOf() !== req.currentUser!.id) {
    throw new NotAuthorizedError();
  }

  const { deletedCount } = await Stock.deleteOne({ _id: req.params.id });

  // console.log("deleted stock item: ", deletedCount);

  res
    .status(201)
    .send(`${deletedCount} StockItem deleted successfully`);
};

export default deleteStockEntry;
