import { Text, View } from "@react-pdf/renderer";
import styles from "../report_styles.js";
import { AddressOut } from "../../types/zod-schemas.js";

const BoxAddress = ({ name, address }: { name: string; address: AddressOut }) => {
  return (
    <View style={{ flexDirection: "column", width: "33%", height: "160px" }}>
      <Text style={styles.subHead}>{name}</Text>
      <View>
        <Text style={styles.addresstext}>{address.receiver}</Text>
        <Text style={styles.addresstext}>{address.house}</Text>
        <Text style={styles.addresstext}>{address.street}</Text>
        <Text style={styles.addresstext}>{address.city}</Text>
        <Text style={styles.addresstext}>{address.pincode}</Text>
      </View>
    </View>
  );
};

export default BoxAddress;
