import { Request, Response, NextFunction } from "express";
import mongoose from "mongoose";
import { Cart } from "../models/cart.js";
import { body } from "express-validator";

export const getCartUser = async (
  req: Request,
  res: Response,
  next: NextFunction
) => {
  // console.log("get cart for user");

  const cart = await Cart.findOne({ userId: req.currentUser?.id });

  // console.log("existing cart: ", cart);

  if (!cart) {
    try {
      const newCart = Cart.build({
        userId: req.currentUser?.id as string,
        cartitems: [],
        totalQuantity: 0,
      });
      await newCart.save();
      // console.log("new cart: ", newCart);
      return res.send(newCart);
    } catch (error) {
      console.log("created errors: ", error);
    }
  }

  res.send(cart);
};

export const replaceCartUser = async (
  req: Request,
  res: Response,
  next: NextFunction
) => {
  const { cartitems, totalQuantity } = req.body;
  const userId = req.currentUser?.id;

  // console.log("replace cart for user: ", userId);
  // console.log("cart items: ", req.body);

  const filter = {
    userId,
  };

  // console.log("filter: ", filter);

  const { deletedCount } = await Cart.deleteMany(filter);
  // console.log("deleted count: ", deletedCount);

  try {
    // if (deletedCount <= 0) {
      // console.log(" Not found going to insert");
      await Cart.findOneAndUpdate(
        filter,
        { cartitems, totalQuantity },
        { new: true, upsert: true }
      );
    // }
  } catch (error) {
    console.log("error in node js: ", error);
  }

  res.status(201).send("Successfully replaced cart");
};
