// import jose, { JWK } from "node-jose";

import joseutils from "./jose-calls.js";
import { UserPayload } from "./types.js";

// const keystore = jose.JWK.createKeyStore();

const { encrypt, decrypt, sign, verify } = joseutils();

export async function generateUserToken(raw: UserPayload) {
  const signedData = await sign(raw);
  return signedData.toString();
}

export async function verifyUserToken(tokenString: string) {
  const verification = await verify(tokenString);
  return verification;
}

export async function encryptData(raw: unknown) {
  const signedData = await sign(raw);

  const verification = await verify(signedData.toString());

  // console.log("final man: ", verification);

  const encryptedData = encrypt(raw);

  const decryptedData = await encryptedData.then((encrypted) =>
    decrypt(encrypted)
  );

  // const output = jose.util.base64url.encode(decryptedData.plaintext);
}
