import { Resolvers } from "../../types/types.js";
import { UpdateMessage } from "../../types/zod-schemas.js";

export const orderResolvers: Resolvers = {
  Query: {
    userAddresses: (_, __, { dataSources }) => {
      return dataSources.orderAPI.getUserAddresses();
    },
    userOrders: (_, __, { dataSources }) => {
      return dataSources.orderAPI.getUserOrders();
    },
    orderByID: (_, { orderid }, { dataSources }) => {
 
      
      return dataSources.orderAPI.getOrder(orderid);
    },
    generateOrderPdf: (_, { orderid }, { dataSources }) => {
      return dataSources.orderAPI.getOrderPdf(orderid);
    },
  },
  OrderItem: {
    orderitem: ({ itemid }, _, { dataSources }) => {
      return dataSources.itemAPI.getPart(itemid);
    },
  },
  OrderEntry: {
    shipping: async ({ shipId }, _, { dataSources }) => {
      if (!shipId) return null;
      const ship_id = shipId.toString();

      return dataSources.orderAPI.getAddress(ship_id);
    },
  },
  Mutation: {
    createOrder: async (_, { cart, value }, { dataSources }) => {
      try {
        const newOrder = await dataSources.orderAPI.createOrder(cart, value);

        // const transformOrder = { ...newOrder, shipping: undefined };
        return {
          code: 200,
          success: true,
          message: "Order created successfully",
          order: newOrder,
        };
      } catch (error: any) {
        console.error("Error creating order:", error);
        return {
          code: error.extensions?.response?.status || 500,
          success: false,
          message: error.extensions?.response?.body || "Internal Server Error",
          order: null,
        };
      }
    },
    updateOrder: async (_, { orderid, status }, { dataSources }) => {
      // console.log("create order: ", cart, " value: ", value);

      try {
        const updateStatus = await dataSources.orderAPI.updateOrder(
          orderid,
          status
        );
        // console.log("New Order Created: ", newOrder);
        const statusMessage = UpdateMessage.safeParse(updateStatus);
        return {
          code: 200,
          success: true,
          message: statusMessage.success
            ? statusMessage.data.message
            : "Invalid return message",
        };
      } catch (error: any) {
        console.error("Error updating order:", error);
        return {
          code: error.extensions?.response?.status || 500,
          success: false,
          message: error.extensions?.response?.body || "Internal Server Error",
        };
      }
    },
    cancelOrder: async (_, { orderid }, { dataSources }) => {
      // console.log("create order: ", cart, " value: ", value);

      try {
        const cancelStatus = await dataSources.orderAPI.cancelOrder(orderid);

        const statusMessage = UpdateMessage.safeParse(cancelStatus);

        // console.log("New Order Created: ", newOrder);
        return {
          code: 200,
          success: true,
          message: statusMessage.success
            ? statusMessage.data.message
            : "Invalid return message",
          // message: cancelStatus?.message as string,
        };
      } catch (error: any) {
        console.error("Error cancelling order:", error);
        return {
          code: error.extensions?.response?.status || 500,
          success: false,
          message: error.extensions?.response?.body || "Internal Server Error",
        };
      }
    },
    completeOrder: async (_, { orderid, shipping }, { dataSources }) => {
      // console.log("create order: ", cart, " value: ", value);

      try {
        const completeStatus = await dataSources.orderAPI.completeOrder(
          orderid,
          shipping
        );

        const statusMessage = UpdateMessage.safeParse(completeStatus);

        // console.log("New Order Created: ", newOrder);
        return {
          code: 200,
          success: true,
          message: statusMessage.success
            ? statusMessage.data.message
            : "Invalid return message",
        };
      } catch (error: any) {
        console.error("Error cancelling order:", error);
        return {
          code: error.extensions?.response?.status || 500,
          success: false,
          message: error.extensions?.response?.body || "Internal Server Error",
        };
      }
    },
    createAddress: async (_, { input }, { dataSources }) => {
      // console.log("create order: ", cart, " value: ", value);

      try {
        const address = await dataSources.orderAPI.createAddress(input);
        // console.log("New Order Created: ", newOrder);
        return {
          code: 200,
          success: true,
          message: "Address created successfully",
          order: address,
        };
      } catch (error: any) {
        console.error("Error creating order:", error);
        return {
          code: error.extensions?.response?.status || 500,
          success: false,
          message: error.extensions?.response?.body || "Internal Server Error",
          order: null,
        };
      }
    },
  },
};
