import { Request, Response, NextFunction } from "express";
import mongoose from "mongoose";
import { BillItem } from "../models/bill-item.js";

export const getBillsByItemId = async (
  req: Request,
  res: Response,
  next: NextFunction
) => {
  // console.log("get invoices entries: ", req.params.itemid);
  let billItemArr = await BillItem.find({
    itemid: req.params.itemid,
  });
  
  res.send(billItemArr);
};

export const getInvoiceById = async (
  req: Request,
  res: Response,
  next: NextFunction
) => {
  // console.log("get invoice entry: ", req.params.id);

  const billItem = await BillItem.findOne({
    _id: new mongoose.Types.ObjectId(req.params.id),
  });

  // console.log(billItem);

  res.send(billItem);
};
