import env from "../config/env.js";
import { RESTDataSource, AugmentedRequest } from "@apollo/datasource-rest";
import { BillInput, BillModel } from "../graphql/models.js";
import { KeyValueCache } from "@apollo/utils.keyvaluecache";
import _ from "lodash";

export default class BillAPI extends RESTDataSource {
  override baseURL = `${env.API_URL}/api/`;
  private token: string;

  constructor(options: { token: string; cache: KeyValueCache }) {
    super(options);
    this.token = options.token;
  }

  override willSendRequest(_path: string, request: AugmentedRequest) {
    request.headers["usertoken"] = this.token;
  }

  async getBillById(billId: string) {
    return await this.get<BillModel>(`invoice/${billId}`);
  }

  async getBillByItemId(itemid: string) {
    return await this.get<BillModel[]>(`invoice/item/${itemid}`);
  }

  async createBill(billitem: BillInput) {
    // console.log("params currently: ", billitem);

    try {
      // return await this.post<ItemModel>(`${type}/new`, {
      return await this.post<BillModel>("invoice/new", {
        body: billitem,
      });
    } catch (error: any) {
      console.log("graphql error: ", error?.extensions);
      return null;
    }
  }

  async updateBill(stockitem: BillInput) {
    // console.log("token currently: ", this.token);

    try {
      // return await this.put<ItemModel>(`${type}/${item.id}`, {
      return await this.put<BillModel>(`invoice/update`, {
        body: stockitem,
      });
    } catch (error: any) {
      console.log("graphql error: ", error?.extensions);
      return null;
    }
  }

  async deleteBill(id: string) {
    // console.log("token currently: ", this.token);

    try {
      return await this.delete<string>(`invoice/delete/${id}`);
    } catch (error: any) {
      console.log("graphql error: ", error?.extensions);
      return null;
    }
  }
}
