import { Request, Response, NextFunction } from "express";
import { Like } from "../models/like.js";

const getLikes = async (req: Request, res: Response, next: NextFunction) => {
  try {
    const likes = await Like.find({ userId: req.currentUser?.id }, 'itemId userId'); // "675d263dab93ec2b8dc1cb53" });
    // console.log("likes: ", likes); //, " for user: ", req.currentUser?.id);
    res.status(201).send(likes);
  } catch (error) {
  }
};

