import mongoose from "mongoose";
const Schema = mongoose.Schema;
const itemtypes = ["Product", "Equipment", "Spare", "Package", "Category"];

interface PartAttrs {
  itemid: string;
  itemtype: string;
  assignid: string;
  assign: string;
  userId: string;
}

export interface PartDoc extends mongoose.Document {
  id: string;
  itemid: string;
  itemtype: string;
  assignid: string;
  assign: string;
  userId: string;
  version: number;
}

interface PartModel extends mongoose.Model<PartDoc> {
  build(attrs: PartAttrs): PartDoc;
}


const partSchema = new mongoose.Schema(
  {
    itemid: {
      type: Schema.Types.ObjectId,
      ref: "Item",
      required: true,
    },
    itemtype: {
      type: String,
      enum: itemtypes,
      // ref: "Product",
      required: true,
    },
    assignid: {
      type: Schema.Types.ObjectId,
      ref: "Item",
      required: true,
    },
    assign: {
      type: String,
      required: true,
    },
    userId: {
      type: Schema.Types.ObjectId,
      ref: "User",
      required: true,
    },
  },
  {
    toJSON: {
      transform(doc, ret: any) {
        ret.id = ret._id;
        delete ret._id;
      },
    },
  }
);

partSchema.index({ itemid: 1, assignid: 1 }, { unique: true });

partSchema.statics.build = (attrs: PartAttrs) => {
  return new Part(attrs);
};

const Part = mongoose.model<PartDoc, PartModel>("Part", partSchema);

export { Part };
