import env from "./config/env.js";
import express from "express";
import http from "http";
import session from "express-session";
import MongoStore from "connect-mongo";

import bodyparser from "body-parser";
import { TrackAPI } from "./datasources/track-api.js";
import ItemAPI from "./datasources/item-api.js";
import PartAPI from "./datasources/part-api.js";
import StockAPI from "./datasources/stock-api.js";
import BillAPI from "./datasources/bill-api.js";
import AuthAPI from "./datasources/auth-api.js";
import CartAPI from "./datasources/cart-api.js";
import OrderAPI from "./datasources/order-api.js";
import cors from "cors";
import cookieParser from "cookie-parser";

import { expressMiddleware } from "@as-integrations/express4";
import { ApolloServer } from "@apollo/server";
import { ApolloServerPluginDrainHttpServer } from "@apollo/server/plugin/drainHttpServer";
import { typeDefs } from "./graphql/schema.js";
import { customResolvers } from "./graphql/resolvers/custom-resolvers.js";
import { rootResolvers } from "./graphql/resolvers/root-resolver.js";

import { currentUser } from "./middleware/current-user.js";
import { errorHandler } from "@manprtickets/common";
import { authRouter } from "./auth/routes/auth_routes.js";
import { itemRouter } from "./item/routes/item_routes.js";
import { partRouter } from "./part/routes/part_routes.js";
import { likeRouter } from "./likes/routes/likes_routes.js";
import { stockRouter } from "./stock/routes/stock_routes.js";
import { billRouter } from "./invoice/routes/bill_routes.js";
import { cartRouter } from "./cart/routes/cart_routes.js";
import { orderRouter } from "./order/routes/order-routes.js";

interface MyContext {
  token?: string;
  dataSources?: {
    trackAPI: TrackAPI;
    itemAPI: ItemAPI;
    partAPI: PartAPI;
    stockAPI: StockAPI;
    billAPI: BillAPI;
    authAPI: AuthAPI;
    cartAPI: CartAPI;
    orderAPI: OrderAPI;
  };
}

const corsOptions = {
  origin: "*",
  methods: ["POST", "PUT", "GET", "OPTIONS", "HEAD", "DELETE", "PATCH"],
  credentials: true,
  // exposedHeaders: ["Set-Cookie"],
};
const app = express();
const MONGODB_URI = `mongodb+srv://${env.MONGO_USER}:${env.MONGO_PASSWORD}@cluster0.q4wsrjb.mongodb.net/${env.MONGO_DEFAULT_DATABASE}?retryWrites=true&w=majority&appName=Cluster0`;
const { json } = bodyparser;
app.use(json());

app.use(cookieParser(env.JWT_KEY));

const httpServer = http.createServer(app);

declare module "express-session" {
  interface SessionData {
    jwt: string;
  }
}

app.use(cors(corsOptions));

app.use(
  session({
    secret: [process.env.JWT_KEY!],
    resave: false,
    // name: "session.web",
    saveUninitialized: false,
    // cookie: { httpOnly: false, sameSite: "none" },
    store: MongoStore.create({
      mongoUrl: MONGODB_URI,
      ttl: 8 * 60 * 60,
      autoRemove: "interval",
      autoRemoveInterval: 5,
    }),
  })
);

app.use((req, res, next) => {
  const usertoken = req.headers["usertoken"];

  if (!req.session?.jwt && usertoken) {
    req.session.jwt = usertoken as string;
  }

  res.header("Access-Control-Allow-Origin", req.headers.origin);
  res.header(
    "Access-Control-Allow-Methods",
    "OPTIONS, GET, POST, PUT, PATCH, DELETE"
  );
  res.setHeader("Access-Control-Allow-Credentials", "true");
  res.header(
    "Access-Control-Allow-Headers",
    "Origin X-Requested-With, Content-Type, Accept, Authorization"
  );

  next();
});

app.use(currentUser);

app.use("/api/users", authRouter);
app.use("/api/item", itemRouter);
app.use("/api/part", partRouter);
app.use("/api/like", likeRouter);
app.use("/api/stock", stockRouter);
app.use("/api/invoice", billRouter);
app.use("/api/cart", cartRouter);
app.use("/api/order", orderRouter);

app.use(errorHandler);

const startServer = async () => {
  const server = new ApolloServer<MyContext>({
    typeDefs,
    resolvers: { Date: { ...customResolvers.Date }, ...rootResolvers },
    plugins: [ApolloServerPluginDrainHttpServer({ httpServer })],
  });
  await server.start();

  app.use(
    "/graphql",
    cors<cors.CorsRequest>(corsOptions),
    express.json(),
    expressMiddleware(server, {
      context: async ({ req }) => {
        const token = req.headers.usertoken as string;
        // console.log("token in graphql: ", token, " headers: ", req.headers);

        const { cache } = server;
        return {
          token,
          dataSources: {
            trackAPI: new TrackAPI({ cache }),
            itemAPI: new ItemAPI({ cache, token }),
            partAPI: new PartAPI({ cache, token }),
            stockAPI: new StockAPI({ cache, token }),
            billAPI: new BillAPI({ cache, token }),
            authAPI: new AuthAPI({ cache, token }),
            cartAPI: new CartAPI({ cache, token }),
            orderAPI: new OrderAPI({ cache, token }),
          },
        };
      },
    })
  );
  httpServer.listen(env.PORT || 8080, () => {
    console.log(`🚀 Server ready at ${process.env.PORT}`);
  });
};

export { startServer, MONGODB_URI };
