import mongoose from "mongoose";
const Schema = mongoose.Schema;

const itemtypes = ["Product", "Equipment", "Spare", "Package"];

interface LikeAttrs {
  itemId: string;
  userId: string;
  // itemType: string;
  // itemModel: string;
}

export interface LikeDoc extends mongoose.Document {
  id: string;
  itemId: string;
  userId: string;
  // itemType: string;
  // itemModel: string;
}

interface LikeModel extends mongoose.Model<LikeDoc> {
  build(attrs: LikeAttrs): LikeDoc;
}

const likeSchema = new mongoose.Schema(
  {
    itemId: {
      type: Schema.Types.ObjectId,
      required: true,
      ref: "Item",
    },
    // itemType: {
    //   type: String,
    //   enum: itemtypes,
    //   required: true,
    // },
    userId: {
      type: Schema.Types.ObjectId,
      ref: "User",
      required: true,
    },
    // itemModel: {
    //   type: String,
    //   required: true,
    //   enum: itemtypes,
    // },
  },
  {
    toJSON: {
      transform(doc, ret: any) {
        ret.id = ret._id;
        delete ret._id;
      },
    },
  }
);

likeSchema.statics.build = (attrs: LikeAttrs) => {
  return new Like(attrs);
};

const Like = mongoose.model<LikeDoc, LikeModel>("Like", likeSchema);

export { Like };
