import { Request, Response, NextFunction } from "express";
import { Like } from "../models/like.js";

const changeLike = async (req: Request, res: Response, next: NextFunction) => {

  const filter = {
    itemId: req.params.itemid,
    userId: req.currentUser?.id,
  };

  // console.log(filter);

  const { deletedCount } = await Like.deleteMany(filter);

  // console.log(deletedCount);

  try {
    if (deletedCount <= 0) {
      // console.log(" Not found going to insert");
      await Like.findOneAndUpdate(filter, {}, { new: true, upsert: true });
    }
  } catch (error) {
    console.log("error in node js: ", error);
  }

  res.status(201).send("Successfully updated");
};

export default changeLike;
