import mongoose from "mongoose";
const Schema = mongoose.Schema;

const itemtypes = ["Product", "Equipment", "Spare", "Package"];
const units = ["No", "Kg", "Lts", "cm", "mm", "mtr"];

export interface StockAttrs {
  itemid: string;
  itemtype: string;
  quantity: number;
  qtytype: string;
  unit: string;
  price: number;
  gst: number;
  discount: number;
  userId: string;
}

export interface StockDoc extends mongoose.Document {
  id: string;
  itemid: string;
  itemtype: string;
  title: string;
  quantity: number;
  qtytype: string;
  unit: string;
  price: number;
  gst: number;
  discount: number;
  userId: string;
}

interface StockModel extends mongoose.Model<StockDoc> {
  build(attrs: StockAttrs): StockDoc;
}

const stockSchema = new mongoose.Schema(
  {
    itemid: {
      type: Schema.Types.ObjectId,
      ref: "item",
      required: true,
    },
    itemtype: {
      type: String,
      enum: itemtypes,
      required: true,
    },
    unit: {
      type: String,
      enum: units,
      required: true,
    },
    qtytype: {
      type: String,
      default: "Stock",
      required: true,
    },
    quantity: {
      type: Number,
      default: 0,
      required: true,
    },
    price: {
      type: Number,
      default: 0.0,
      required: true,
    },
    gst: {
      type: Number,
      default: 0.0,
      required: true,
    },
    discount: {
      type: Number,
      default: 0.0,
      required: true,
    },
    userId: {
      type: Schema.Types.ObjectId,
      ref: "User",
      required: true,
    },
  },
  {
    toJSON: {
      transform(doc, ret: any) {
        ret.id = ret._id;
        delete ret._id;
      },
    },
  }
);

stockSchema.statics.build = (attrs: StockAttrs) => {
  return new Stock(attrs);
};

const Stock = mongoose.model<StockDoc, StockModel>(
  "Stock",
  stockSchema
);

export { Stock };
