import { Request, Response, NextFunction } from "express";
import { userPayloadSchema } from "../../lib/types.js";
import { NotAuthorizedError } from "@manprtickets/common";

const getCurrentUser = (req: Request, res: Response) => {

  // console.log("entry into current user route: ", req.currentUser);
  
  // res.send({ currentUser: req.currentUser || null });
  const user =  req.currentUser && userPayloadSchema.safeParse(req.currentUser);

  // console.log("user: ", user);
  

  if (user) {
    // throw new NotAuthorizedError();
    res.send(user.data);
  } else {
    res.send(null);
  }
};

export default getCurrentUser;
