import { Resolvers } from "../../types/types.js";

export const cartResolvers: Resolvers = {
  Query: {
    cartUser: (_, __, { dataSources }) => {
      return dataSources.cartAPI.getCartUser();
    },
  },
  CartItem: {
    item: ({ itemid }, _, { dataSources }) => {
       return dataSources.itemAPI.getPart(itemid);
    },
  },
  Mutation: {
    replaceCart: async (_, { input }, { dataSources }) => {

        if (!input) {
          throw new Error("Input is required");
        }
        const { cartitems, totalQuantity } = input;

      try {
        const cartMessage = await dataSources.cartAPI.replaceCart(cartitems, totalQuantity);
        // console.log("cartMessage: ", cartMessage);
        
        return {
          code: 200,
          success: true,
          message: "Cart replaced successfully",
        };
      } catch (err: any) {
        // console.log("Error thrown in here");

        return {
          code: err.extensions.response.status,
          success: false,
          message: err.extensions.response.body,
        };
      }
    },
  },
};
