import { z } from "zod";
import { OrderStatus } from "./order-status.js";

export const UpdateMessage = z.object({
  message: z.string(),
});

const addressSchema = z.object({
  id: z.string().transform((val) => (val ? undefined : val)),
  email: z.string(),
  receiver: z.string(),
  phone: z.string(),
  house: z.string(),
  street: z.string(),
  city: z.string(),
  pincode: z.string(),
});

export type AddressIn = z.input<typeof addressSchema>;
export type AddressOut = z.output<typeof addressSchema>;

export enum ItemTypes {
  Product = "Product",
  Spare = "Spare",
  Equipment = "Equipment",
  Package = "Package",
  Category = "Category",
}

const numberSchema = z.coerce
  .number()
  .min(0.01)
  .max(1000)
  .nonnegative()
  .multipleOf(0.01);
const stringSchema = z.string().min(6);

const itemSchema = z.object({
  id: z.string(),
  itemType: z.enum(ItemTypes),
  title: stringSchema,
  image: stringSchema,
  description: stringSchema,
  unit: z.string().optional(),
  dimensions: stringSchema.optional(),
  quantity: numberSchema.optional(),
  price: numberSchema.optional(),
  totalPrice: numberSchema.optional(),
  gst: numberSchema.optional(),
  discount: numberSchema.optional(),
});

export type ItemType = z.infer<typeof itemSchema>;

export const orderItemSchema = z.object({
  orderitem: itemSchema,
  orderId: z.string().optional(),
  quantity: numberSchema,
  price: numberSchema,
  totalPrice: numberSchema,
});

export type OrderItemType = z.infer<typeof orderItemSchema>;

export const orderSchema = z.object({
  id: z.string(),
  status: z.enum(OrderStatus),
  items: z.array(orderItemSchema),
  orderDate: z.coerce.date(),
  totalQuantity: numberSchema,
  orderValue: numberSchema,
  shipping: addressSchema,
});

export type OrderType = z.infer<typeof orderSchema>;
