import mongoose from "mongoose";
const Schema = mongoose.Schema;

const itemtypes = ["Product", "Equipment", "Spare", "Package"];
const qtyactions = ["Purchase", "Sale", "Return", "Obsolete", "Defective"];
const units = ["No", "Kg", "Lts", "cm", "mm", "mtr"];

export interface BillItemAttrs {
  itemid: string;
  itemtype: string;
  // itemModel: string;
  quantity: number;
  qtytype: string;
  unit: string;
  price: number;
  gst: number;
  discount: number;
  billtotal: number;
  shipping: number;
  userId: string;
}

export interface BillItemDoc extends mongoose.Document {
  id: string;
  itemid: string;
  itemtype: string;
  // itemModel: string;
  title: string;
  quantity: number;
  qtytype: string;
  unit: string;
  price: number;
  gst: number;
  discount: number;
  billtotal: number;
  shipping: number;
  userId: string;
}

interface BillItemModel extends mongoose.Model<BillItemDoc> {
  build(attrs: BillItemAttrs): BillItemDoc;
}

const billItemSchema = new mongoose.Schema(
  {
    itemid: {
      type: Schema.Types.ObjectId,
      required: true,
      ref: "items",
    },
    itemtype: {
      type: String,
      enum: itemtypes,
      required: true,
    },
    unit: {
      type: String,
      enum: units,
      required: true,
    },
    qtytype: {
      type: String,
      enum: qtyactions,
      default: "Invoice",
      required: true,
    },
    quantity: {
      type: Number,
      default: 0,
      required: true,
    },
    price: {
      type: Number,
      default: 0.00,
      required: true,
    },
    gst: {
      type: Number,
      default: 0.00,
      required: true,
    },
    discount: {
      type: Number,
      default: 0.00,
      required: true,
    },
    billtotal: {
      type: Number,
      default: 0.00,
      required: true,
    },
    shipping: {
      type: Number,
      default: 0.00,
      required: true,
    },
    userId: {
      type: Schema.Types.ObjectId,
      ref: "User",
      required: true,
    },
    // itemModel: {
    //   type: String,
    //   required: true,
    //   enum: itemtypes,
    // },
  },
  {
    toJSON: {
      transform(doc, ret: any) {
        ret.id = ret._id;
        delete ret._id;
      },
    },
  }
);

billItemSchema.statics.build = (attrs: BillItemAttrs) => {
  return new BillItem(attrs);
};

const BillItem = mongoose.model<BillItemDoc, BillItemModel>(
  "BillItem",
  billItemSchema
);

export { BillItem };
