import mongoose from "mongoose";
const Schema = mongoose.Schema;

export interface AddressAttrs {
  userId: string;
  email: string;
  receiver: string;
  house: string;
  street: string;
  city: string;
  pincode: string;
  phone: string;
}

export interface AddressDoc extends mongoose.Document {
  id: string
  userId: string;
  email: string;
  receiver: string;
  house: string;
  street: string;
  city: string;
  pincode: string;
  phone: string;
}

interface AddressModel extends mongoose.Model<AddressDoc> {
  build(attrs: AddressAttrs): AddressDoc;
}

const addressSchema = new mongoose.Schema(
  {
    userId: {
      type: Schema.Types.ObjectId,
      ref: "User",
      required: true,
    },
    email: {
      type: String,
      required: true,
    },
    receiver: {
      type: String,
      required: true,
    },
    house: {
      type: String,
      required: true,
    },
    street: {
      type: String,
      required: true,
    },
    pincode: {
      type: String,
      required: true,
    },
    city: {
      type: String,
      required: true,
    },
    phone: {
      type: String,
      required: true,
    },
  },
  {
    toJSON: {
      transform(doc, ret: any) {
        ret.id = ret._id;
        delete ret._id;
      },
    },
  }
);

addressSchema.statics.build = (attrs: AddressAttrs) => {
  return new Address(attrs);
};

const Address = mongoose.model<AddressDoc, AddressModel>(
  "Address",
  addressSchema
);

export { Address };
