import { Request, Response, NextFunction } from "express";
import { BadRequestError } from "@manprtickets/common";
// import jwt from "jsonwebtoken";
import { User } from "../models/user.js";
import { generateUserToken } from "../../lib/session.js";
import { validCredentialsSchema } from "../../lib/types.js";

const postSignup = async (req: Request, res: Response, next: NextFunction) => {
  // const { email, password, first_name, last_name } = req.body;

  const user_valid = validCredentialsSchema.safeParse(req.body);

  if (!user_valid.success) {
    return res.status(400).send(user_valid.error);
  }
  const { email, password, firstName, lastName, phone } = user_valid.data;

  // console.log("email in auth signup: ", email, "name: ", firstName, lastName);

  const existingUser = await User.findOne({ email });

  if (existingUser) {
    throw new BadRequestError("Email in use");
  }
  // console.log("existing user: ", existingUser);

  const user = User.build({ email, password, firstName, lastName, phone });
  // console.log("user: ", user);

  await user.save();

  // Generate JWT
  // const userJwt = jwt.sign(
  //   {
  //     id: user.id,
  //     email: user.email,
  //   },
  //   process.env.JWT_KEY!
  // );
  const userJwt = await generateUserToken({
    id: user.id,
    email: user.email,
    firstName: user.firstName,
    lastName: user.lastName,
  });

  // Store it on session object
  req.session.jwt = userJwt;
  // req.session = {
  //   jwt: userJwt,
  // };

  res.status(200).send({
    user: {
      id: user.id,
      firstName: user.firstName,
      lastName: user.lastName,
      email: user.email,
      phone: user.phone,
    },
    token: userJwt,
  });

  // res.status(201).send(user);
};

export default postSignup;
