import { Request, Response, NextFunction } from "express";
import * as _ from "lodash";
import { NotFoundError } from "@manprtickets/common";
import { BillItem } from "../models/bill-item.js";

const updateBill = async (req: Request, res: Response, next: NextFunction) => {
  const {
    itemid,
    quantity,
    unit,
    qtytype,
    price,
    gst,
    discount,
    billtotal,
    shipping,
  } = req.body;

  // console.log("body: ", req.body);

  const billItem = await BillItem.findById(itemid);

  if (!billItem) {
    throw new NotFoundError();
  }

  // console.log("invoice item: ", billItem);

  billItem.set({
    quantity: quantity,
    unit: unit,
    qtytype: qtytype,
    price: price,
    gst: gst,
    discount: discount,
    billtotal: billtotal,
    shipping: shipping,
  });

  await billItem.save();

  res.status(201).send(billItem);
};

export default updateBill;
