import { Request, Response, NextFunction } from "express";
import { Part } from "../../part/models/part";
import mongoose from "mongoose";
import { assign } from "lodash";

export const getAssignedParts = async (
  req: Request,
  res: Response,
  next: NextFunction
) => {
  try {
    const assign_id = req.params.assignid;
    const item_type = req.params.itemtype;

    // console.log("assign_id: ", assign_id, " item type: ", item_type);

    // const parts = await Part.find({ assignid: assign_id, itemtype: item_type });

    const parts = await Part.aggregate([
      {
        $match: {
          //   $expr: { $eq: ["$assignid", { $toObjectId: assign_id }] },

          $and: [
            { itemtype: item_type },
            { assignid: new mongoose.Types.ObjectId(assign_id) },
          ],
        },
      },
      {
        $project: {
          itemid: { $toString: "$itemid" },
          itemtype: 1,
          assignid: { $toString: "$assignid" },
          assign: 1,
          id: { $toString: "$_id" },
        },
      },
    ]);

    // console.log("parts: ", parts);

    // const products = await Part.aggregate([
    //   {
    // $match: {
    //   assignid: assign_id,
    //   assignstring: assign_type,
    // },
    //   },
    //   { $addFields: { prodId: { $toObjectId: "$itemid" } } },
    //   { $project: { _id: 0, prodId: 1, itemid: 1 } },
    //   {
    //     $lookup: {
    //       from: "items",
    //       localField: "itemid",
    //       foreignField: "_id",
    //       as: "result",
    //     },
    //   },
    //   { $unwind: { path: "$result" } },
    //   {
    //     $project: {
    //       id: "$itemid",
    //       title: "$result.title",
    //       image: "$result.image",
    //       type: "Product",
    //     },
    //   },
    // ]);

    // console.log("products: ", products);
    res.status(201).send(parts);
  } catch (error) {
    console.log(error);
  }
};
