import { Text, View } from "@react-pdf/renderer";
import styles from "../report_styles.js";
import { OrderItemType } from "../../types/zod-schemas.js";

const BOMTableBody = ({ items }: { items: OrderItemType[] }) => {
  return (
    <View style={{ flexDirection: "column" }}>
      {items.map((orderitem, index) => (
        <View style={styles.tbodyRow} key={orderitem.orderitem.id}>
          <View style={styles.tcells}>
            <Text style={styles.text}>{index+1}</Text>
          </View>
          <View style={styles.tcellxl}>
            <Text style={styles.text}>{orderitem.orderitem.title}</Text>
          </View>
          <View style={styles.tcells}>
            <Text style={styles.text}>{orderitem.price}</Text>
          </View>
          <View style={styles.tcells}>
            <Text style={styles.text}>{orderitem.quantity}</Text>
          </View>
          <View style={styles.tcellm}>
            <Text style={styles.text}>{orderitem.totalPrice}</Text>
          </View>
          <View style={styles.tcells}>
            <Text style={styles.text}>5%</Text>
          </View>
          <View style={styles.tcells}>
            <Text style={styles.text}>CGST</Text>
          </View>
          <View style={styles.tcellm}>
            <Text style={styles.text}>20.00</Text>
          </View>
          <View style={styles.tcellm}>
            <Text style={styles.text}>{orderitem.totalPrice}</Text>
          </View>
        </View>
      ))}
    </View>
  );
};

export default BOMTableBody;
