import express from "express";
import { body } from "express-validator";

import getCurrentUser from "./current-user.js";
import postSignin from "./signin.js";
import postSignout from "./signout.js";
import postSignup from "./signup.js";
import { validateRequest } from "@manprtickets/common";
import { Request, Response, NextFunction } from "express";
import { getUserById } from "./index.js";
// import { currentUser } from "../middleware/current-user.ts.old";

const router = express.Router();

router.get("/currentuser", getCurrentUser);
router.get("/:id", getUserById);
router.post(
  "/signin",
  // async (req: Request, res: Response, next: NextFunction) => {
  //   console.log(req.body);
  //   next();
  // },
  [
    body("email").isEmail().withMessage("Email must be valid"),
    body("password")
      .trim()
      .notEmpty()
      .withMessage("You must supply a password"),
  ],
  validateRequest,
  postSignin
);
router.post("/signout", postSignout);
router.post(
  "/signup",
  // [
  //   body("email").isEmail().withMessage("Email must be valid"),
  //   body("password")
  //     .trim()
  //     .isLength({ min: 6, max: 20 })
  //     .withMessage("Password must be between 6 and 20 characters"),
  // ],
  // validateRequest,
  postSignup
);

export { router as authRouter };
