import { Resolvers } from "../../types/types.js";

export const billResolvers: Resolvers = {
  Query: {
    billById: (_, { billid }, { dataSources }) => {
      return dataSources.billAPI.getBillById(billid);
    },
    billByItemId: (_, { itemid }, { dataSources }) => {
      return dataSources.billAPI.getBillByItemId(itemid);
    },
  },
  Mutation: {
    createBill: async (_, { input }, { dataSources }) => {
      try {
        // console.log("New item created: ", input);
        const newBillItem = await dataSources.billAPI.createBill(input);
        // console.log("returning new bill: ", newBillItem);
        return {
          code: 200,
          success: true,
          message: "Bill created successfully",
          item: newBillItem,
        };
      } catch (err: any) {
        return {
          code: err.extensions.response.status,
          success: false,
          message: err.extensions.response.body,
          item: null,
        };
      }
    },
    updateBill: async (_, { input }, { dataSources }) => {
      try {
        const editBillItem = await dataSources.billAPI.updateBill(input);
        // console.log("New item created: ", newItem);

        return {
          code: 200,
          success: true,
          message: "Bill updated successfully",
          item: editBillItem,
        };
      } catch (err: any) {
        return {
          code: err.extensions.response.status,
          success: false,
          message: err.extensions.response.body,
          item: null,
        };
      }
    },
    deleteBill: async (_, { billId }, { dataSources }) => {
      try {
        const message = await dataSources.billAPI.deleteBill(billId);
        // console.log("return value: ", message);

        return {
          code: 200,
          success: true,
          message: "Bill deleted successfully",
          item: message as string,
        };
      } catch (err: any) {
        return {
          code: err.extensions.response.status,
          success: false,
          message: err.extensions.response.body,
          item: "",
        };
      }
    },
  },
};
