import { Request, Response, NextFunction } from "express";
import {
  NotAuthorizedError,
  NotFoundError,
  RequestValidationError,
} from "@manprtickets/common";
import { Part } from "../models/part";
import { Item } from "../../item/models/item";
import mongoose from "mongoose";
import { MongoBulkWriteError } from "mongodb";

export const assignItemsToCategory = async (
  req: Request,
  res: Response,
  next: NextFunction
) => {
  // console.log("id string: ", req.params.idStr);

  const selectedItem = await Item.findById(req.params.idStr);

  if (!selectedItem) {
    throw new NotFoundError();
  }

  if (selectedItem.userId.valueOf() !== req.currentUser!.id) {
    throw new NotAuthorizedError();
  }

  const { assignIDs, assign } = req.body;

  // console.log(assignIDs, " type: ", assign, " selected: ", selectedItem);

  try {
    const partsDocs = assignIDs.map((id: string) => {
      return {
        itemid: selectedItem.id,
        itemtype: selectedItem.itemType,
        assignid: id,
        assign: assign,
        userId: req.currentUser!.id,
      };
    });

    // console.log(partsDocs);

    await Part.insertMany(partsDocs);
  } catch (error) {
    // console.log("error is here");

    if (error instanceof MongoBulkWriteError && error.code === 11000) {
      throw RequestValidationError;
    }
    // // console.log(typeof error);
  }

  res
    .status(201)
    .send(`selected ${selectedItem.itemType} assigned to ${assign}`);
};

export const getAssignments = async (
  req: Request,
  res: Response,
  next: NextFunction
) => {
  // const selectedItem = await Part.find({ itemid: req.params.idStr }).populate([
  //   { path: "itemid", select: "title description" },
  // ]);
  // console.log("called me");

  const assign = req.params.assign;

  // console.log("assign string: ", assign);

  try {
    const assignments = await Part.aggregate([
      {
        $match: { assignid: new mongoose.Types.ObjectId(assign) },
      },
      {
        $lookup: {
          from: "items",
          localField: "assignid",
          foreignField: "_id",
          as: "category",
        },
      },
      {
        $lookup: {
          from: "items",
          localField: "itemid",
          foreignField: "_id",
          as: "parts",
        },
      },
      {
        $unwind: "$category",
      },
      {
        $unwind: "$parts",
      },
      {
        $group: {
          _id: { id: "$category._id", name: "$category.title" },
          assigned: {
            $push: {
              id: "$parts._id",
              itemType: "$parts.itemType",
              title: "$parts.title",
              description: "$parts.description",
              image: "$parts.image",
            },
          },
        },
      },
    ]).exec();

    // console.log(assignments);

    res.status(201).send(assignments);
  } catch (error) {
    console.log("error in assignments: ", error);
  }
};

