import { gql } from "graphql-tag";

export const typeDefs = gql`
  scalar Date

  type Query {
    "Query to get tracks array for the homepage grid"
    tracksForHome: [Track!]!
    "Fetch a specific track, provided a track's ID"
    track(id: ID!): Track!
    "Fetch a specific module, provided a module's ID"
    module(id: ID!): Module!
    "Fetch all categories"
    categories: [Category]
    "Common query to get item like Category, Equipment, Product etc for the select input"
    itemsBySearch(search: String!, itemType: String!): [Item!]
    "Get Item by itemid"
    itemById(id: ID!): Item!
    "Get Stock by itemid"
    stockByItemId(itemid: ID!): [StockEntry]
    "Get Stock by stockid"
    stockById(stockid: ID!): StockEntry
    "Get Stock by itemType"
    stockByItemType(itemType: String!): [ItemStock!]!
    "get items by type for filling the select list in assign page"
    itemsByType(itemType: String!): [TypeEntry]
    "Assignment of Item to category. Get items by Category"
    assignments(assign: String!): [Assignments]
    "Get Part or Subcategory by assigned Category"
    assignedParts(assignid: ID!, itemtype: String!): [Part!]!
    billById(billid: ID!): BillEntry
    billByItemId(itemid: ID!): [BillEntry]
    currentUser: User
    cartUser: Cart!
    "get shipping address by userId"
    userAddresses: [Address!]!
    "get orders by user"
    userOrders: [OrderEntry!]!
    "get order by Id"
    orderByID(orderid: ID!): OrderEntry!
    "generate a pdf file for order by Id"
    generateOrderPdf(orderid: ID!): File
    likes: [LikeItem]
  }

  type Mutation {
    incrementTrackViews(id: ID!): IncrementTrackViewsResponse!
    createItem(input: ItemInput!): CreateItemResponse!
    updateItem(input: ItemInput!): CreateItemResponse!
    deleteItem(id: ID!): DeleteItemResponse!
    createAssignments(input: AssignmentInput!): CreateAssignmentResponse!
    createStock(input: StockInput!): CreateStockResponse!
    updateStock(input: StockInput!): CreateStockResponse!
    deleteStock(stockId: ID!): DeleteStockResponse!
    createBill(input: BillInput!): CreateBillResponse!
    updateBill(input: BillInput!): CreateBillResponse!
    deleteBill(billId: ID!): DeleteBillResponse!
    signin(input: SignInput!): SignInResponse!
    signup(input: SignUpInput!): SignInResponse!
    signout: SignOutResponse!
    replaceCart(input: CartInput!): ReplaceCartResponse!
    createOrder(cart: CartInput!, value: Float!): CreateOrderResponse!
    updateOrder(orderid: ID!, status: String!): UpdateOrderResponse!
    cancelOrder(orderid: ID!): UpdateOrderResponse!
    completeOrder(orderid: ID!, shipping: AddressInput!): UpdateOrderResponse!
    createAddress(input: AddressInput!): CreateAddressResponse!
    like(itemId: ID!): UpdateLikeResponse!
  }

  type File {
    id: String!
    filename: String!
    fileurl: String!
  }

  type LikeItem {
    item: Item
    userId: String!
  }

  input AddressInput {
    id: ID!
    email: String!
    receiver: String!
    phone: String!
    house: String!
    street: String!
    pincode: String!
    city: String!
  }

  type Address {
    id: ID!
    email: String!
    receiver: String!
    phone: String!
    house: String!
    street: String!
    pincode: String!
    city: String!
  }

  type CreateAddressResponse {
    code: Int!
    success: Boolean!
    message: String!
    shipid: String
  }

  type UpdateLikeResponse {
    code: Int!
    success: Boolean!
    message: String!
  }

  type UpdateOrderResponse {
    code: Int!
    success: Boolean!
    message: String!
  }

  type CreateOrderResponse {
    code: Int!
    success: Boolean!
    message: String!
    order: OrderEntry
  }

  type OrderEntry {
    id: ID!
    userId: String!
    status: String!
    items: [OrderItem!]!
    orderDate: Date!
    expiresAt: Date!
    totalQuantity: Int!
    orderValue: Float!
    shipping: Address
  }

  type OrderItem {
    orderitem: Item!
    orderId: String!
    quantity: Int!
    price: Float!
    totalPrice: Float!
  }

  input CartInput {
    cartitems: [CartItemInput!]!
    totalQuantity: Int!
  }

  type Cart {
    cartitems: [CartItem!]!
    totalQuantity: Int!
    userId: String!
  }

  input CartItemInput {
    itemid: String!
    quantity: Int!
    price: Float!
    totalPrice: Float!
    limitQty: Int!
  }

  type CartItem {
    item: Item!
    quantity: Int!
    price: Float!
    totalPrice: Float!
    limitQty: Int!
  }

  type Part {
    id: ID!
    partitem: Item!
    itemtype: String!
    assignparent: Item!
    assign: String!
  }

  input SignInput {
    email: String!
    password: String!
  }

  input SignUpInput {
    email: String!
    password: String!
    firstName: String!
    lastName: String!
    phone: String!
  }

  type ReplaceCartResponse {
    code: Int!
    success: Boolean!
    message: String!
  }

  type SignInResponse {
    code: Int!
    success: Boolean!
    message: String!
    user: User
    token: String!
  }

  type SignOutResponse {
    code: Int!
    success: Boolean!
    message: String!
  }

  type User {
    id: ID!
    email: String!
    firstName: String!
    lastName: String!
    phone: String!
  }

  input BillInput {
    itemid: String!
    itemtype: String!
    quantity: Int!
    qtytype: String!
    unit: String!
    price: Float!
    gst: Float!
    discount: Float!
    billtotal: Float!
    shipping: Float!
    userId: String!
  }

  type BillEntry {
    id: String!
    itemid: String!
    itemtype: String!
    quantity: Int!
    qtytype: String!
    unit: String!
    price: Float!
    gst: Float!
    discount: Float!
    billtotal: Float!
    shipping: Float!
    userId: String!
  }

  input StockInput {
    itemid: String!
    itemtype: String!
    quantity: Int!
    qtytype: String!
    unit: String!
    price: Float!
    gst: Float!
    discount: Float!
    userId: String!
  }

  "A generic item type that can represent different entities like Category, Equipment, Product, etc."
  type Item {
    id: ID!
    itemType: String!
    title: String!
    description: String!
    image: String!
    unit: String
    dimensions: String
    userId: String!
  }

  type ItemStock {
    id: ID!
    item: Item!
    quantity: Int!
    qtytype: String!
    price: Float!
    gst: Float!
    discount: Float!
    userId: String!
  }

  type StockEntry {
    id: String!
    itemid: String!
    itemtype: String!
    quantity: Int!
    qtytype: String!
    unit: String!
    price: Float!
    gst: Float!
    discount: Float!
    userId: String!
  }

  type CreateBillResponse {
    code: Int!
    success: Boolean!
    message: String!
    item: BillEntry
  }

  type DeleteBillResponse {
    code: Int!
    success: Boolean!
    message: String!
    item: String
  }

  type CreateStockResponse {
    code: Int!
    success: Boolean!
    message: String!
    item: StockEntry
  }

  type DeleteStockResponse {
    code: Int!
    success: Boolean!
    message: String!
    item: String!
  }

  type TestPart {
    id: ID!
    name: String!
  }

  type Assignments {
    _id: TestPart!
    assigned: [Assignment!]!
  }

  type Assignment {
    id: ID!
    itemType: String!
    title: String!
    description: String!
    image: String!
  }

  input AssignmentInput {
    id: ID!
    assignIDs: [ID!]!
    assign: String!
  }

  type TypeEntry {
    key: ID!
    label: String!
  }

  type CreateAssignmentResponse {
    code: Int!
    success: Boolean!
    message: String!
    item: String
  }

  type CreateItemResponse {
    code: Int!
    success: Boolean!
    message: String!
    item: Item
  }

  type DeleteItemResponse {
    code: Int!
    success: Boolean!
    message: String!
    item: String!
  }

  type IncrementTrackViewsResponse {
    "Similar to HTTP status code, represents the status of the mutation"
    code: Int!
    "Indicates whether the mutation was successful"
    success: Boolean!
    "Human-readable message for the UI"
    message: String!
    "Newly updated track after a successful mutation"
    track: Track
  }

  type Category {
    id: ID!
    title: String!
    description: String!
    image: String!
    userId: String!
  }

  input ItemInput {
    id: ID!
    itemType: String!
    title: String!
    description: String!
    image: String!
    unit: String!
    dimensions: String!
    userId: String!
    referid: String!
  }

  "A track is a group of Modules that teaches about a specific topic"
  type Track {
    id: ID!
    "The track's title"
    title: String!
    "The track's main Author"
    author: Author!
    "The track's illustration to display in track card or track page detail"
    thumbnail: String
    "The track's approximate length to complete, in minutes"
    length: Int
    "The number of modules this track contains"
    modulesCount: Int
    "The track's complete description, can be in markdown format"
    description: String
    "The number of times a track has been viewed"
    numberOfViews: Int
    "The track's complete array of Modules"
    modules: [Module!]!
  }

  "Author of a complete Track or a Module"
  type Author {
    id: ID!
    "Author's first and last name"
    name: String!
    "Author's profile picture"
    photo: String
  }

  "A Module is a single unit of teaching. Multiple Modules compose a Track"
  type Module {
    id: ID!
    "The module's title"
    title: String!
    "The module's length in minutes"
    length: Int
    "The module's text-based description, can be in markdown format. In case of a video, it will be the enriched transcript"
    content: String
    "The module's video url, for video-based modules"
    videoUrl: String
  }
`;
