import { Request, Response, NextFunction } from "express";
import * as _ from "lodash";
import { NotFoundError } from "@manprtickets/common";
import { Stock } from "../models/stock";

const updateStockEntry = async (
  req: Request,
  res: Response,
  next: NextFunction
) => {
  const { itemid, quantity, unit, price, gst, discount } = req.body;

  // console.log("body: ", req.body);

  const stockItem = await Stock.findById(itemid);

  if (!stockItem) {
    throw new NotFoundError();
  }

  // console.log("stock item: ", stockItem);

  stockItem.set({
    quantity: quantity,
    unit: unit,
    // qtytype: "Stock",
    price: price,
    gst: gst,
    discount: discount,
  });

  await stockItem.save();

  res.status(201).send(stockItem);
};

export default updateStockEntry;
