import env from "../config/env.js";
import { RESTDataSource, AugmentedRequest } from "@apollo/datasource-rest";
import {
  AssignmentModel,
  AssignModel,
  CartModel,
  ItemModel,
  LikeModel,
  PartModel,
  TypeEntry,
} from "../graphql/models.js";
import { KeyValueCache } from "@apollo/utils.keyvaluecache";
import _ from "lodash";

export default class ItemAPI extends RESTDataSource {
  override baseURL = `${env.API_URL}/api/`;
  private token: string;

  constructor(options: { token: string; cache: KeyValueCache }) {
    super(options);
    this.token = options.token;
  }

  override willSendRequest(_path: string, request: AugmentedRequest) {
    request.headers["usertoken"] = this.token;
  }

  async getItemsBySearch(searchStr: string, itemType: string) {
    const searchString = !searchStr ? "all" : searchStr;
    const typeString = itemType;
    // console.log("search string: ", searchStr, " type: ", typeString);

    return await this.get<ItemModel[]>(
      `item/search/${itemType}/${searchString}/`
    );
   
  }

  async getItemById(id: string) {
    // console.log("id: ",id);
    
    return await this.get<ItemModel>(`item/${id}`);
  }

  async getPart(itemid: string) {
    return await this.get<ItemModel>(`item/${itemid}`);
  }

  async getItemsByType(itemType: string) {
    return await this.get<TypeEntry[]>(`item/select/${itemType}`);
  }

  async getAssignments(assign: string) {
    return await this.get<AssignmentModel[]>(`part/${assign}`);
  }

  async getAssignedParts(assignid: string, item_type: string) {
    return await this.get<PartModel[]>(`part/${item_type}/${assignid}`);
  }

  async getLikes() {
    const likes = await this.get<LikeModel[]>("like/");
    // console.log("likes in api: ", likes);
    return likes;
  }

  async changeLike(itemId: string) {
    try {
      return await this.patch<{ message: string }>(`like/${itemId}`);
    } catch (error: any) {
      console.log("graphql error: ", error?.extensions);
      return null;
    }
  }

  async createAssignments(item: AssignModel) {
    // console.log("params currently: ", item);

    try {
      // return await this.post<ItemModel>(`${type}/new`, {
      return await this.post<string>(`part/assign/${item.id}`, {
        body: item,
      });
    } catch (error: any) {
      console.log("graphql error: ", error?.extensions);
      return null;
    }
  }

  async createItem(item: ItemModel) {
    // console.log("params currently: ", item);

    try {
      // return await this.post<ItemModel>(`${type}/new`, {
      return await this.post<ItemModel>("item/new", {
        body: item,
      });
    } catch (error: any) {
      console.log("graphql error: ", error?.extensions);
      return null;
    }
  }

  async updateItem(item: ItemModel) {
    // console.log("token currently: ", this.token);

    try {
      // return await this.put<ItemModel>(`${type}/${item.id}`, {
      return await this.put<ItemModel>(`item/${item.id}`, {
        body: item,
      });
    } catch (error: any) {
      console.log("graphql error: ", error?.extensions);
      return null;
    }
  }

  async deleteItem(id: string) {
    // console.log("token currently: ", this.token);

    try {
      return await this.delete<string>(`item/${id}`);
    } catch (error: any) {
      console.log("graphql error: ", error?.extensions);
      return null;
    }
  }
}
