import { Request, Response, NextFunction } from "express";
import { Order } from "../models/order.js";
import { OrderStatus } from "../../types/order-status.js";
import { CartItemAttrs } from "../../cart/models/cart.js";
import { Address } from "../models/address.js";

export const createOrder = async (
  req: Request,
  res: Response,
  next: NextFunction
) => {
  const EXPIRATION_WINDOW_SECONDS = 15 * 60;

  const {
    cart: { cartitems, totalQuantity },
    value,
  } = req.body;

  const expiration = new Date();
  expiration.setSeconds(expiration.getSeconds() + EXPIRATION_WINDOW_SECONDS);

  const neworder = new Order({
    userId: req.currentUser!.id,
    status: OrderStatus.Created,
    orderDate: new Date(),
    orderValue: value,
    // cacheCart: req.body.items,
    expiresAt: expiration,
    items: [],
    totalQuantity: totalQuantity,
    shipId: undefined,
  });

  // console.log("new order: ", neworder);

  const orderitems = cartitems.map((itemobj: CartItemAttrs) => ({
    itemid: itemobj.itemid,
    orderId: neworder._id,
    // itemtype: itemobj.itemtype,
    // itemModel: itemobj.itemtype,
    quantity: itemobj.quantity,
    price: itemobj.price,
    totalPrice: itemobj.totalPrice,
  }));

  neworder.items = orderitems;

  // console.log("new order: ", neworder);

  await neworder.save();

  res.send(neworder);
};

export const createAddress = async (
  req: Request,
  res: Response,
  next: NextFunction
) => {
  // console.log(req.body);
  const { email, receiver, phone, house, street, pincode, city } = req.body;

  const newaddress = new Address({
    userId: req.currentUser!.id,
    email,
    receiver,
    house,
    street,
    city,
    pincode,
    phone,
  });

  try {
    await newaddress.save();
  } catch (error) {
    console.log("error: ", error);
  }

  // console.log("new: ", newaddress);
  res.send({ shipId: newaddress.id });
};
