import { Text, View } from "@react-pdf/renderer";
import styles from "../report_styles.js";


type MyProps = {
  name: string;
  num_fld: string;
  date_fld: string;
  style: {};
};

const BoxNoDate: React.FC<MyProps> = ({ name, num_fld, date_fld, style }) => {
  return (
    <View style={[{ flexDirection: "column", width: "50%" }, style]}>
      <Text>{`${name} No: ${num_fld}`}</Text>
      <Text>{`${name} Date: ${date_fld}`}</Text>
    </View>
  );
};

export default BoxNoDate;
