import { Request, Response, NextFunction } from "express";
import { Order } from "../models/order.js";
import { NotAuthorizedError, NotFoundError } from "@manprtickets/common";
import { Address } from "../models/address.js";
import { Payment } from "../models/payment.js";

export const updateOrder = async (
  req: Request,
  res: Response,
  next: NextFunction
) => {
  // console.log("order status: ", req.body);
  const order = await Order.findById(req.params.orderid);
  if (!order) {
    throw new NotFoundError();
  }

  if (order.userId.valueOf() !== req.currentUser!.id) {
    throw new NotAuthorizedError();
  }

  order.set({
    status: req.body.status,
    // shippingId: req.body.shipping?
  });

  await order.save();

  res.send({ message: "Order udpated successfully" });
};

export const completeOrder = async (
  req: Request,
  res: Response,
  next: NextFunction
) => {
  const { shipping } = req.body;
  // console.log("order complete: ", shipping);
  const order = await Order.findById(req.params.orderid);
  if (!order) {
    throw new NotFoundError();
  }

  if (order.userId.valueOf() !== req.currentUser!.id) {
    throw new NotAuthorizedError();
  }

  const { id, email, receiver, phone, house, street, pincode, city } =
    shipping;

  let address = null;

  let existingAddress = false;

  if (id && id !== "") {
    address = await Address.findById(id);
    if (!address) {
      throw new NotFoundError();
    }
    existingAddress = true;
  } else {
    address = new Address({
      userId: req.currentUser!.id,
      email,
      receiver,
      house,
      street,
      city,
      pincode,
      phone,
    });
  }

  // const newaddress = new Address({
  //   userId: req.currentUser!.id,
  //   receiver,
  //   house,
  //   street,
  //   city,
  //   pincode,
  //   phone,
  //   email,
  // });

  console.log("shipping id: ", address?.id);

  const payObject = new Payment({
    orderId: order.id,
    stripeId: "adfbbc2332",
    amount: order.orderValue,
  });

  order.set({
    shipId: address?.id,
    status: "Complete",
  });

  if (!existingAddress) {
    await address.save();
  }

  await order.save();
  await payObject.save();

  res.send({ message: "Order completed successfully" });
};
