export const flattenObject = (obj: any): any => {
  const flattened: any = {};

  Object.keys(obj).forEach((key) => {
    const value = obj[key];
    if (typeof value === "object" && value !== null && !Array.isArray(value)) {
      Object.assign(flattened, flattenObject(value));
    } else {
      flattened[key] = value;
    }
  });

  return flattened;
};

export const formattedPrice = (inputPrice: number) =>
  `$ ${inputPrice.toFixed(2)}`;

export const formattedDate = (inputDate: Date) =>
  `${inputDate.getDate()}-${inputDate.getMonth()}-${inputDate.getFullYear()}`;

export const formattedIDNumber = (idStr: string, maxLength: number) => {
  if (idStr.length > maxLength) {
    return idStr.slice(0, maxLength);
  } else {
    return idStr;
  }
};
