import mongoose from "mongoose";
const Schema = mongoose.Schema;

const units = ["No", "Kg", "Lts", "mm", "cm", "meter"];
const itemtypes = ["Product", "Equipment", "Spare", "Package", "Category"];

interface ItemAttrs {
  itemType: string;
  title: string;
  description: string;
  image?: string;
  userId: string;
}

interface ItemDoc extends mongoose.Document {
  itemType: string;
  title: string;
  description: string;
  image?: string;
  userId: string;
}

// interface ProductAttrs {
//   title: string;
//   description: string;
//   image?: string;
//   userId: string;
//   unit: string;
//   dimensions: string;
// }

// interface ProductDoc extends mongoose.Document {
//   title: string;
//   description: string;
//   image?: string;
//   userId: string;
//   unit: string;
//   dimensions: string;
// }

interface ItemModel extends mongoose.Model<ItemDoc> {
  build(attrs: ItemAttrs): ItemDoc;
}

// interface ProductModel extends mongoose.Model<ProductDoc> {
//   build(attrs: ProductAttrs): ProductDoc;
// }

const itemSchema = new Schema(
  {
    itemType: { type: String, enum: itemtypes, required: true, index: true },
    title: {
      type: String,
      required: true,
    },
    description: {
      type: String,
      required: true,
    },
    image: {
      type: String,
    },
    userId: {
      type: Schema.Types.ObjectId,
      ref: "User",
      required: true,
    },
  },
  {
    discriminatorKey: "itemType",
    collection: "items",
    toJSON: {
      transform(doc, ret: any) {
        ret.id = ret._id;
        delete ret._id;
      },
    },
  }
);

itemSchema.statics.build = (attrs: ItemAttrs) => {
  return new Item(attrs);
};

const Item = mongoose.model<ItemDoc, ItemModel>("Item", itemSchema);

const productSchema = new Schema({
  unit: {
    type: String,
    enum: units,
    required: true,
  },
  dimensions: {
    type: String,
    required: true,
  },
});

const categorySchema = new Schema({});
const equimentSchema = new Schema({
  dimensions: {
    type: String,
    required: true,
  },
});
const packageSchema = new Schema({
  dimensions: {
    type: String,
    required: true,
  },
});
const spareSchema = new Schema({
  dimensions: {
    type: String,
    required: true,
  },
});

itemSchema.index({ title: "text", description: "text", dimensions: "text" });

// const Product = Item.discriminator<ProductDoc, ProductModel>("Product", productSchema);

const Category = Item.discriminator("Category", categorySchema);
const Product = Item.discriminator("Product", productSchema);
const Equipment = Item.discriminator("Equipment", equimentSchema);
const Package = Item.discriminator("Package", packageSchema);
const Spare = Item.discriminator("Spare", spareSchema);

export { Item, Product, Equipment, Package, Spare, Category };
