import {
  requireAuth,
} from "@manprtickets/common";
import express from "express";

import postNewBill from "./new.js";
import { getBillsByItemId, getInvoiceById } from "./index.js";
import updateBill from "./update.js";
import deleteBill from "./delete.js";

const router = express.Router();

router.get("/:id", getInvoiceById);
router.get("/item/:itemid", requireAuth, getBillsByItemId);

router.post(
  "/new",
  requireAuth,
  postNewBill
);

router.put(
  "/update",
  requireAuth,
  updateBill
);

router.delete("/delete/:id", requireAuth, deleteBill);

export { router as billRouter };
