import { requireAuth } from "@manprtickets/common";
import express from "express";

import postNewStockEntry from "./new";
import {
  getStockById,
  getStockByItemId,
  getStockByItemType,
  getStockByType,
} from ".";
import updateStockEntry from "./update";
import deleteStockEntry from "./delete";

const router = express.Router();

router.get("/:id", getStockById);

router.post("/new", requireAuth, postNewStockEntry);

router.put("/update", requireAuth, updateStockEntry);

router.delete("/delete/:id", requireAuth, deleteStockEntry);

router.get("/item/:itemid", getStockByItemId);

router.get("/type/:itemtype", getStockByItemType);

router.get("/:itemtype", getStockByType);

export { router as stockRouter };
