import mongoose from "mongoose";
const Schema = mongoose.Schema;

const itemtypes = ["Product", "Equipment", "Spare", "Package"];

export interface CartItemAttrs {
  itemid: string;
  // itemtype: string;
  // itemModel: string;
  // name: string;
  quantity: number;
  totalPrice: number;
  price: number;
}

interface CartItemDoc extends mongoose.Document {
  id: string
  itemid: string;
  // itemtype: string;
  // name: string;
  quantity: number;
  limitQty: number;
  totalPrice: number;
  price: number;
}

interface CartItemModel extends mongoose.Model<CartItemDoc> {
  build(attrs: CartItemAttrs): CartItemDoc;
}

const cartItemSchema = new mongoose.Schema(
  {
    itemid: {
      type: Schema.Types.ObjectId,
      required: true,
      ref: "Item",
    },
    // itemtype: {
    //   type: String,
    //   enum: itemtypes,
    //   required: true,
    // },
    // name: {
    //   type: String,
    //   required: true,
    // },
    quantity: {
      type: Number,
      default: 0,
      required: true,
    },
    limitQty: {
      type: Number,
      default: 0,
      required: true,
    },
    price: {
      type: Number,
      default: 0.0,
      required: true,
    },
    totalPrice: {
      type: Number,
      default: 0.0,
      required: true,
    },
  },
  {
    toJSON: {
      transform(doc, ret: any) {
        ret.id = ret._id;
        delete ret._id;
      },
    },
  }
);

// cartItemSchema.statics.build = (attrs: CartItemAttrs) => {
//   return new CartItem(attrs);
// };

// const CartItem = mongoose.model<CartItemDoc, CartItemModel>(
//   "CartItem",
//   cartItemSchema
// );

interface CartAttrs {
  userId: string;
  cartitems: [typeof cartItemSchema] | [];
  totalQuantity: number;
}

interface CartDoc extends mongoose.Document {
  userId: string;
  cartitems: [typeof cartItemSchema] | [];
  totalQuantity: number;
}

interface CartModel extends mongoose.Model<CartDoc> {
  build(attrs: CartAttrs): CartDoc;
}

const cartSchema = new mongoose.Schema(
  {
    userId: {
      type: Schema.Types.ObjectId,
      ref: "User",
      required: true,
    },
    cartitems: {
      type: [cartItemSchema],
      // default: [],
      required: true
    },
    totalQuantity: {
      type: Number,
      default: 0,
      required: true,
    },
  },
  {
    toJSON: {
      transform(doc, ret: any) {
        ret.id = ret._id;
        delete ret._id;
      },
    },
  }
);

cartSchema.statics.build = (attrs: CartAttrs) => {
  return new Cart(attrs);
};

const Cart = mongoose.model<CartDoc, CartModel>("Cart", cartSchema);

export { Cart };
