import { requireAuth } from "@manprtickets/common";
import express from "express";
import createNewItem from "./new.js";
import { getItemById, getItemBySearch, selectItemsByType } from "./index.js";
import updateItem from "./update.js";
import { deleteItemById } from "./delete.js";

const router = express.Router();

router.get("/search/:itemtype/:keystr", getItemBySearch);
router.get("/:idstr", getItemById);
router.get("/select/:typeStr", selectItemsByType);

router.post("/new", requireAuth, createNewItem);
router.put("/:id", requireAuth, updateItem);
router.delete("/:id", requireAuth, deleteItemById);


export { router as itemRouter };
