import { Resolvers } from "../../types/types.js";

export const authResolvers: Resolvers = {
  Query: {
    currentUser: (_, __, { dataSources }) => {
      return dataSources.authAPI.getCurrentUser();
    },
  },
  Mutation: {
    signin: async (_, { input }, { dataSources }) => {
      try {
        const result = await dataSources.authAPI.signin(input);
        // console.log("result: ", result);

        if (!result) {
          return {
            code: 401,
            success: false,
            message: "Invalid credentials",
            user: undefined,
            token: "",
          };
        }
        const { user, token } = result;
        return {
          code: 200,
          success: true,
          message: "Signed In successfully",
          user: user,
          token: token,
        };
      } catch (err: any) {
        return {
          code: err.extensions.response.status,
          success: false,
          message: err.extensions.response.body,
          user: undefined,
          token: "",
        };
      }
    },
    signup: async (_, { input }, { dataSources }) => {
      try {
        const result = await dataSources.authAPI.signup(input);
        // console.log("result: ", result);

        if (!result) {
          return {
            code: 401,
            success: false,
            message: "Invalid credentials",
            user: undefined,
            token: "",
          };
        }
        const { user, token } = result;
        return {
          code: 200,
          success: true,
          message: "Signed Up successfully",
          user: user,
          token: token,
        };
      } catch (err: any) {
        return {
          code: err.extensions.response.status,
          success: false,
          message: err.extensions.response.body,
          user: undefined,
          token: "",
        };
      }
    },
    signout: async (_, __, { dataSources }) => {
      try {
        await dataSources.authAPI.signout();

        return {
          code: 200,
          success: true,
          message: "Signed out",
        };
      } catch (err: any) {
        return {
          code: err.extensions.response.status,
          success: false,
          message: err.extensions.response.body,
        };
      }
    },
  },
};
