import env from "../config/env.js";
import { RESTDataSource, AugmentedRequest } from "@apollo/datasource-rest";
import { KeyValueCache } from "@apollo/utils.keyvaluecache";
import _ from "lodash";
import {
  AddressModel,
  CartItemModel,
  CartModel,
  ItemModel,
  OrderModel,
  FileModel,
} from "../graphql/models.js";
import { AddressIn, AddressOut } from "../types/zod-schemas.js";

export default class OrderAPI extends RESTDataSource {
  override baseURL = `${env.API_URL}/api/`;
  private token: string;

  constructor(options: { token: string; cache: KeyValueCache }) {
    super(options);
    this.token = options.token;
  }

  override willSendRequest(_path: string, request: AugmentedRequest) {
    request.headers["usertoken"] = this.token;
  }

  async getUserAddresses() {
    return await this.get<AddressModel[]>("order/address/");
  }

  async getAddress(id: string) {
    return await this.get<AddressModel>(`order/address/${id}`);
  }

  async getUserOrders() {
    return await this.get<OrderModel[]>("order/");
  }

  async getOrder(orderid: string) {
    return await this.get<OrderModel>(`order/${orderid}`);
  }

  async getOrderPdf(orderid: string) {
    return await this.get<FileModel>(`order/genpdf/${orderid}`);
  }

  async createOrder(cart: Omit<CartModel, "userId">, value: number) {
    try {
      return await this.post<OrderModel>("order/", {
        body: { cart, value },
      });
    } catch (error: any) {
      console.log("graphql error: ", error?.extensions);
      return null;
    }
  }

  async updateOrder(orderid: string, status: string) {
    try {
      return await this.patch<{ message: string }>(`order/${orderid}`, {
        body: { status },
      });
    } catch (error: any) {
      console.log("graphql error: ", error?.extensions);
      return null;
    }
  }

  async cancelOrder(orderid: string) {
    try {
      return await this.delete<{ message: string }>(`order/${orderid}`);
    } catch (error: any) {
      console.log("graphql error: ", error?.extensions);
      return null;
    }
  }

  // async completeOrder(orderid: string, shipping: AddressModel) {
  async completeOrder(orderid: string, shipping: AddressOut) {
    try {
      return await this.patch<{ message: string }>(
        `order/complete/${orderid}`,
        {
          body: { shipping },
        }
      );
    } catch (error: any) {
      console.log("graphql error: ", error?.extensions);
      return null;
    }
  }

  // async createAddress(input: Omit<AddressModel, "id" | "userId">) {
  async createAddress(input: Omit<AddressIn, "id">) {
    try {
      return await this.post<{ shipId: string }>("order/address", {
        body: { input },
      });
    } catch (error: any) {
      console.log("graphql error: ", error?.extensions);
      return null;
    }
  }
}
