import { z } from "zod";
import validator from "validator";

const { isEmail, isMobilePhone } = validator;

export const validCredentialsSchema = z.object({
  email: z.string().refine(isEmail, { message: "Invalid email address" }),
  password: z.string().min(6).max(20),
  firstName: z.string().min(4),
  lastName: z.string().min(4),
  phone: z.string().refine(isMobilePhone, { message: "Invalid phone number" }),
});

export type ValidCredentials = z.infer<typeof validCredentialsSchema>;

export const loginParamsSchema = z.object({
  email: z.string().refine(isEmail, { message: "Invalid email address" }),
  password: z.string().min(6).max(20),
});

export type LoginParams = z.infer<typeof loginParamsSchema>;

export const userPayloadSchema = z.object({
  id: z.string(),
  email: z.string().email(),
  firstName: z.string(),
  lastName: z.string(),
});

export type UserPayload = z.infer<typeof userPayloadSchema>;

export const cartItemSchema = z.object({
  itemid: z.string(),
  itemtype: z.string(),
  itemModel: z.string(),
  name: z.string(),
  quantity: z.number(),
  totalPrice: z.number(),
  price: z.number(),
});

export type CartItem = z.infer<typeof cartItemSchema>;

export const userCartSchema = z.object({
  userId: z.string(),
  items: z.array(cartItemSchema),
  firstName: z.string(),
  lastName: z.string(),
});

export type UserCart = z.infer<typeof userCartSchema>;
