import { GraphQLScalarType, Kind } from "graphql";
import { Resolvers } from "../../types/types.js";
import { UpdateMessage } from "../../types/zod-schemas.js";

export const itemResolvers: Resolvers = {
  Query: {
    itemsBySearch: (_, { search, itemType }, { dataSources }) => {
      return dataSources.itemAPI.getItemsBySearch(search, itemType);
    },
    itemById: (_, { id }, { dataSources }) => {
      return dataSources.itemAPI.getItemById(id);
    },
    itemsByType: (_, { itemType }, { dataSources }) => {
      return dataSources.itemAPI.getItemsByType(itemType);
    },
    assignments: (_, { assign }, { dataSources }) => {
      return dataSources.itemAPI.getAssignments(assign);
    },
    assignedParts: (_, { assignid, itemtype }, { dataSources }) => {
      return dataSources.itemAPI.getAssignedParts(assignid, itemtype);
    },
    likes: (_, __, { dataSources }) => {
      return dataSources.itemAPI.getLikes();
    },
  },
  Part: {
    partitem: ({ itemid }, _, { dataSources }) => {
      return dataSources.itemAPI.getPart(itemid);
    },
    assignparent: ({ assignid }, _, { dataSources }) => {
      return dataSources.itemAPI.getPart(assignid);
    },
  },
  LikeItem: {
    item: ({ itemId }, _, { dataSources }) => {
      return dataSources.itemAPI.getPart(itemId);
    },
  },
  Mutation: {
    createAssignments: async (_, { input }, { dataSources }) => {
      // console.log("input received here: ", input);

      try {
        const assignMessage = await dataSources.itemAPI.createAssignments(
          input
        );
        // console.log("New item created: ", newItem);

        return {
          code: 200,
          success: true,
          message: "Items assignments created successfully",
          item: assignMessage,
        };
      } catch (err: any) {
        // console.log("Error thrown in here");

        return {
          code: err.extensions.response.status,
          success: false,
          message: err.extensions.response.body,
          item: "Assignments failed",
        };
      }
    },
    createItem: async (_, { input }, { dataSources, token }) => {
      // console.log("token received here: ", token);

      try {
        const newItem = await dataSources.itemAPI.createItem(input);
        // console.log("New item created: ", newItem);

        return {
          code: 200,
          success: true,
          message: "Item created successfully",
          item: newItem,
        };
      } catch (err: any) {
        return {
          code: err.extensions.response.status,
          success: false,
          message: err.extensions.response.body,
          item: null,
        };
      }
    },
    updateItem: async (_, { input }, { dataSources }) => {
      try {
        const editItem = await dataSources.itemAPI.updateItem(input);
        // console.log("New item created: ", newItem);

        return {
          code: 200,
          success: true,
          message: "Item updated successfully",
          item: editItem,
        };
      } catch (err: any) {
        return {
          code: err.extensions.response.status,
          success: false,
          message: err.extensions.response.body,
          item: null,
        };
      }
    },
    deleteItem: async (_, { id }, { dataSources }) => {
      try {
        const message = await dataSources.itemAPI.deleteItem(id);
        // console.log("return value: ", message);

        return {
          code: 200,
          success: true,
          message: "Item deleted successfully",
          item: message as string,
        };
      } catch (err: any) {
        return {
          code: err.extensions.response.status,
          success: false,
          message: err.extensions.response.body,
          item: "",
        };
      }
    },
    like: async (_, { itemId }, { dataSources }) => {
      try {
        const likeStatus = await dataSources.itemAPI.changeLike(itemId);
        // console.log("New Order Created: ", newOrder);
        const statusMessage = UpdateMessage.safeParse(likeStatus);
        return {
          code: 200,
          success: true,
          message: statusMessage.success
            ? statusMessage.data.message
            : "Invalid return message",
        };
      } catch (error: any) {
        console.error("Error updating order:", error);
        return {
          code: error.extensions?.response?.status || 500,
          success: false,
          message: error.extensions?.response?.body || "Internal Server Error",
        };
      }
    },
  },
};
