import { Text, View } from "@react-pdf/renderer";
import styles from "../report_styles.js";

const BOMTableHead = () => {
  return (
    <View style={styles.theadRow}>
      <View style={styles.tcells}>
        <Text style={styles.theadText}>Sl. No.</Text>
      </View>
      <View style={styles.tcellxl}>
        <Text style={styles.theadText}>Description</Text>
      </View>
      <View style={styles.tcells}>
        <Text style={styles.theadText}>Unit Price</Text>
      </View>
      <View style={styles.tcells}>
        <Text style={styles.theadText}>Qty</Text>
      </View>
      <View style={styles.tcellm}>
        <Text style={styles.theadText}>Net Amt</Text>
      </View>
      <View style={styles.tcells}>
        <Text style={styles.theadText}>Tax Rate</Text>
      </View>
      <View style={styles.tcells}>
        <Text style={styles.theadText}>Tax Type</Text>
      </View>
      <View style={styles.tcellm}>
        <Text style={styles.theadText}>Tax Amt</Text>
      </View>
      <View style={styles.tcellm}>
        <Text style={styles.theadText}>Total Amt</Text>
      </View>
    </View>
  );
};

export default BOMTableHead;
